function compressImage(imgToCompress, resizingFactor, quality, callback) {
    // showing the compressed image
    const canvas = document.createElement("canvas");
    const context = canvas.getContext("2d");
    let compressedImageBlob;
    let img_url;
  
    const originalWidth = imgToCompress.width ?? 300;
    const originalHeight = imgToCompress.height ?? 300;

    if(originalWidth > 2200 || originalHeight > 2200){
      resizingFactor = 0.5;
    }

    if(originalWidth > 1920 || originalHeight > 1920){
      resizingFactor = 0.6;
    }


    const canvasWidth = originalWidth * resizingFactor;
    const canvasHeight = originalHeight * resizingFactor;
  
    canvas.width = canvasWidth;
    canvas.height = canvasHeight;
    context.drawImage(
        imgToCompress,
        0,
        0,
        originalWidth * resizingFactor,
        originalHeight * resizingFactor
      );
  
    // reducing the quality of the image
    canvas.toBlob(
      (blob) => {
        if (blob) {
            compressedImageBlob = blob;
            img_url = URL.createObjectURL(compressedImageBlob);
            // let reader = new FileReader();
            //     reader.readAsDataURL(blob); 
            //     reader.onloadend = function() {
            //     let base64data = reader.result;           
            //   }
            callback(blob, img_url);
        }
      },
    
      "image/jpeg",
      quality
    );
}

function imgToCanvas(imgFile){
  return new Promise((resolve, reject) => {
      const canvas = document.createElement("canvas");
      const imagen = new Image();
      imagen.onload = () => {
          canvas.width = imagen.width;
          canvas.height = imagen.height;
          canvas.getContext("2d").drawImage(imagen, 0, 0);
          canvas.toBlob(
              (blob) => {
                  if (blob === null) {
                      return reject(blob);
                  } else {
                      resolve(canvas);
                  }
              },
              "image/jpeg",
          );
      };
      imagen.src = URL.createObjectURL(imgFile);
  });
}

export { compressImage, imgToCanvas};