import { topbar } from '../libraries/Topbar';
import { turpial, Turpial } from '../libraries/turpial';

window.topbar = topbar;
window.turpial = new Turpial({
	public_path: location_host,
	loadModulesOnRoute: false,
});
window.__HOST = turpial.host



let app = document.querySelector(".tripod-app");
app = turpial.mount(app, `<div class="svelte-app-child index-module"></div>`);

app = app.attachShadow({mode: 'open'});

window.TripodApplication.target = app;

import("../App.svelte").then((module)=>{ 
	let Module = module.default;
	new Module(window.TripodApplication)
})