<script>
import NotificationBox from './../Elements/Notifications/NotificationBox.svelte';
import SimpleModal from 'tripod-styles/src/components/Modals/SimpleModal.svelte';
import { ChannelData } from "../Multimedia/ChannelData.store";
import FilesAdmin from 'tripod-styles/src/components/FilesAdmin.svelte';
import { secondsToString, bytesTo } from '../../libraries/VideoHelpers.js';
import { VideoController } from "../Multimedia/partials/VideoController.js";
import LoaderElement from "../Elements/LoaderElement.svelte";
import { compressImage, imgToCanvas } from '../../libraries/compressImage';
import { ThumbnailController } from '../Multimedia/partials/ThumbnailController';
import { turpial } from '../../libraries/turpial';
export let externalItems;
export let updateOrigin = ()=>{};
export let paginator;

let filesData = [];
let localPoster;
let isPosterResponse = false;
let changeCustomThumbnail = false;
let video_uploader = new VideoController();
let showFilesMenu = false;
let showContent = true;
let stateEditor = false;

$: filesData = $ChannelData.proccesing_files, showFilesMenu = true;;

function showContentHanddler(){
    showContent = !showContent;
}

function openNameEditor(){
    stateEditor = true;
}

function closeNameEditor(){
    stateEditor = false;
}

function updateVideo(uid, type, src = false){
    if (type === "title") {
        if (videoName.length > 0) {
            video_uploader.updateVideo(uid, {title: videoName});
            stateEditor = false;
        } else {
            videoName = "";
            stateEditor = false;
            return;
        }
    }
    if (type === "thumbnail" && src) {
        isPosterResponse = true;
        video_uploader.updateVideo(uid, {thumbnail: src}, (data) => {
            localPoster = data.data.thumbnail;
            externalItems.map((item, key)=>{
                if (item.uid === uid) {
                    item.thumbnail = data.data.thumbnail;
                    ChannelData.updateProcessThumbnailById(uid, data.data.thumbnail);
                    filesData = $ChannelData.proccesing_files;
                    updateOrigin(externalItems);
                }
            })
            isPosterResponse = false;
        })
    }
}
function updatePoster(file){
    localPoster = file.thumbnail;
    filesData.map((item, key)=>{
        if (item.uid === file.uid) {
            item.thumbnail = file.thumbnail;
            ChannelData.updateProcessThumbnailById(item.uid, file.thumbnail);
        }
    })
    localPoster = localPoster;
}

async function uploadCustomThumbnail(e, selected_video = false, is_upload = false){
    let file = e.target.files[0];
    
    let promiseData = await imgToCanvas(file);
    compressImage(promiseData, 0.8, 0.8, (blob, url)=>{
        let base64 = URL.createObjectURL(blob);
        console.log(base64);
        if(selected_video.uid !== null){
            console.log(selected_video.uid);
            let thumbnail_controller = new ThumbnailController();
            thumbnail_controller.uploadCustomThumbnail(selected_video.uid, blob, (param)=>{
                paginator.get().submit();
                ChannelData.customThumbnailUpdater(selected_video.uid, param.data.custom_thumbnail);
                localPoster = param.data.thumbnail;
            }, is_upload);
        }
    });
}

</script>
{#if filesData.length >= 1 && showFilesMenu}
    <div class="files-container flex-column has-background-white">
        <div class="full-width flex-center-space-between px-alt-4 py-alt-2 primary-gradient">
            <p class="has-text-white fw500 font-size-14">Subidos {$ChannelData.completed}/{filesData.length} elementos</p>
            <div class="flex-center gap-17">
                <!-- svelte-ignore a11y-click-events-have-key-events -->
                <span class="p-alt-0  arrow-drop {showContent === true ? "change" : ""} is-clickable" on:click={()=>{showContentHanddler()}}>
                    <i class="icon-tripod-arrow-small-down has-text-white font-size-12"></i>
                </span>
                <!-- svelte-ignore a11y-click-events-have-key-events -->
                {#if $ChannelData.completed === filesData.length}
                    <span class="p-alt-0 is-clickable" on:click={()=> showFilesMenu = false}>
                        <i class="icon-tripod-times has-text-white font-size-12"></i>
                    </span>               
                {/if}
            </div>
        </div>
        {#if showContent}
            <div class="flex-column full-width custom-scrollbar-gray" style="max-height: 219px; overflow-y: auto; overflow-x: hidden;">
                {#each filesData as file}
                    <div class="flex-center-space-between full-width px-alt-4 py-alt-3 gap-17">
                        <div class="flex-center-flex-start gap-17 full-width" style="{file.loadStatus === 'is_failed' ? 'width: 40%;' : 'width: 65%;'}">
                            <!-- svelte-ignore missing-declaration -->
                            <!-- svelte-ignore a11y-click-events-have-key-events -->
                            <!-- svelte-ignore a11y-autofocus -->
                            <SimpleModal classes="has-responsive-height" has_responsive_tripod={"has-responsive-tripod"} closeOnClickOverlay={true} externStyles={"display: flex; flex-direction: column;"}>
                                <svelte:fragment slot="header">
                                    <div class="full-width flex-column gap-13">
                                        <h2 class="font-size-24 fw700">Vista previa</h2>
                                    </div>
                                    </svelte:fragment>
                                <svelte:fragment slot="content" let:modal let:hide>
                                    {#if file.percentage < 100}
                                        {#if file.loadStatus === "is_failed"}
                                                <!-- content here -->
                                                <NotificationBox>
                                                    <svelte:fragment slot="title">
                                                        {file.errorDetails.msg}
                                                    </svelte:fragment>
                                                    Ha ocurrido un error con tu conexión.
                                                </NotificationBox>
                                            {:else}
                                                <!-- else content here -->
                                                <div class="flex-column gap-14 mb-alt-4">
                                                    <p class="fw500">Procesando ({file.percentage}%)</p>
                                                    <progress class="progress full-width" value="{file.percentage}" max="100"></progress>
                                                </div>    
                                        {/if}
                                    {/if}
                                    <div class="flex-column full-width" style="{file.loadStatus === "is_failed" ? "height: 82%;" : file.percentage < 100 ? "height: 89%;" : "height: 100%;"}">
                                        {#if file.percentage === 100}
                                            <iframe
                                                src="https://stream.3pod.io/stream/watch/{file.uid}?poster={localPoster}?time={turpial.random_string(5)}"
                                                frameborder="0"
                                                style="border: none; min-height: 300px;"
                                                allow="accelerometer; gyroscope; autoplay; encrypted-media; picture-in-picture;"
                                                allowfullscreen="true"
                                                class="stream-3pod-iframe"
                                                title="3Pod video"
                                            ></iframe>
                                        {/if}
                                        {#if file.percentage < 100 && file.is_thumbnail_ready === false && file.loadStatus != "is_failed"}
                                            <div class="full-size flex-center" style="{file.loadStatus === "is_failed" ? "max-height: 327px;" : ""} min-height: 300px;">
                                                <LoaderElement showMessage={false} />
                                            </div>
                                        {/if}
                                        {#if file.percentage < 100 && file.is_thumbnail_ready === true}
                                            <div class="full-size flex-center" style="position: relative; {file.loadStatus === "is_failed" ? "opacity: 1;" : "opacity: 0.3;"} background-image: url('{file?.thumbnails_group[0]}'); background-repeat: no-repeat; background-size: 100% 100%; min-height: 300px;">
                                                <!-- <img src="{file?.thumbnails_group[0]}" alt="img" style="width: 100%; height: auto;"> -->
                                                {#if file.loadStatus != "is_failed"}
                                                     <!-- content here -->
                                                     <LoaderElement showMessage={false} style_container={"position: absolute;"} style_loader={"background: transparent;"} />
                                                {/if}
                                            </div>
                                        {/if}
                                        <div class="main-info-container flex-column-flex-start gap-13 full-width">
                                            <div class="title-container full-width flex-center-space-between mt-alt-4">
                                                {#if stateEditor}
                                                    <div class="flex-center-flex-end full-width" style="position: relative;">
                                                        <input autoFocus type="text" class="input is-fullwidth font-size-20 fw700" value="{file.title}" on:input={(e)=>videoName = e.target.value} style="padding-right: 5rem;">
                                                        <span class="title-editor-icons flex-center gap-alt-7">
                                                            <i class="icon-tripod-times has-text-danger is-clickable" on:click={() => closeNameEditor()}></i>
                                                            <i class="icon-tripod-checklist has-text-success is-clickable" on:click={() => updateVideo(file.uid, "title")}></i>
                                                        </span>
                                                    </div>
                                                {:else}
                                                    <span class="flex-center gap-13">
                                                        <h3 class="fw700">{file.title}</h3>
                                                        <i class="icon-tripod-edit font-restricted-size-18 is-clickable {file.update_title === true || file.update_title === undefined ? 'has-text-primary' :'has-text-grey'}" on:click={(e)=>{openNameEditor()}} style="{file.loadStatus === "is_failed" ? 'pointer-events: none !important;' : ""} {file.update_title === true || file.update_title === undefined ? 'pointer-events: all !important;' :'pointer-events: none !important;'}"></i>
                                                    </span>
                                                {/if}
                                            </div>
                                            <div class="flex-column-flex-start full-width gap-alt-8">
                                                {#if file.percentage >= 100}
                                                    <div class="flex-center gap-13">
                                                        <div class="flex-column-flex-start gap-alt-8">
                                                            <p class="font-size-16 has-text-dark fw600">Duración</p>
                                                            <p class="font-size-16 has-text-dark fw600">Peso</p>
                                                        </div>
                                                        <div class="flex-column-flex-start gap-alt-8">
                                                            <p class="fw500 font-size-14">{secondsToString(file.duration)} minutos</p>
                                                            <p class="fw500 font-size-14">{bytesTo(file.size, "mb")} MB</p>
                                                        </div>
                                                    </div>
                                                {/if}
                                                <div class="flex-column-flex-start full-width gap-alt-8">
                                                    <p class="font-size-16 has-text-dark fw600">
                                                        Miniatura
                                                    </p>
                                                    <div class="thumbnails-container flex-center-flex-start full-width gap-alt-8">
                                                        {#each file.thumbnails_group as img}
                                                            {#if file.is_thumbnail_ready === true && file.loadStatus !== 'is_failed'}
                                                                 <!-- content here -->
                                                                <div class="thumbnail-item {localPoster === img ? 'active' : ''}">
                                                                    <img src="{img}" alt="thumbnail" class="is-clickable" on:click={(e)=>{updateVideo(file.uid, "thumbnail", e.target.src)}} style="{isPosterResponse ? 'pointer-events: none !important;' : ''} {file.update_thumbnails === true || file.update_thumbnails === undefined ? 'pointer-events: all !important;' : 'pointer-events: none !important;'}">
                                                                </div>
                                                            {:else}
                                                                <div class="thumbnail-item" style="background: #B4C0C9;">
                                                                </div>
                                                            {/if}
                                                        {/each}
                                                        {#if !file.custom_thumbnail}
                                                            {#if file.loadStatus !== 'is_failed' && file.update_thumbnails === true || file.update_thumbnails === undefined}
                                                                <div class="thumbnail-item">
                                                                    <FilesAdmin Classes={"has-small-responsive"} textSizing={"font-size-12"} imgStyles={"width: 32px; height: 32px;"} Styles="width: 100%; height: 100%;" file={file} fileUploader={uploadCustomThumbnail} />
                                                                </div>
                                                                {:else}  
                                                                <div class="thumbnail-item">

                                                                </div>                                          
                                                            {/if}
                                                        {:else}
                                                            <div class="thumbnail-item is-relative {localPoster === file.custom_thumbnail ? 'active' : ''}">
                                                                <button type="button" style="position: absolute; top: 0; right: 0; border-radius: 0px 8px; opacity: 0.42;" on:click={()=>{
                                                                    changeCustomThumbnail = !changeCustomThumbnail;
                                                                }}>
                                                                    <i class="font-size-16 icon-tripod-times has-text-danger"></i>
                                                                </button>
                                                                {#if changeCustomThumbnail}
                                                                        <!-- content here -->
                                                                    <div class="has-background-white p-alt-2" style="position: absolute; top: 32px; right: 0; border-radius: 8px; border: 1px solid #E1E8ED;">
                                                                        <p class="fw500 has-text-grey">Reemplazar</p>
                                                                        <input type="file" class="file-input formElements" name="bns" on:change={(e) => uploadCustomThumbnail(e, file)}>
                                                                    </div>
                                                                {/if}
                                                                <img src="{file.custom_thumbnail + "?time=" + turpial.random_string(5)}" alt="thumbnail" class="is-clickable" on:click={(e)=>{updateVideo(file.uid, "thumbnail", e.target.src)}} style="{isPosterResponse ? 'pointer-events: none !important;' : ''}">
                                                            </div>
                                                        {/if}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </svelte:fragment>
                                <div slot="trigger" let:active on:click={()=>{updatePoster(file)}}>
                                    <i class="icon-tripod-edit has-text-grey font-size-18"></i>
                                </div>
                            </SimpleModal>
                            <span class="full-width" style="width: calc(100% - 38px); max-width: 198px;">
                                <p class="has-text-dark file-title fw500">{file.title}</p>
                            </span> 
                        </div>
                        <div class="flex-center-flex-end gap-13 full-width" style="{file.loadStatus === 'is_failed' ? 'width: 60%;' : 'width: 35%;'}">
                            {#if file.loadStatus !== 'is_failed'}
                                <span class="has-text-grey fw500">{file.percentage}%</span>            
                            {/if}
                            {#if file.loadStatus === 'is_loading' || file.loadStatus === 'is_completed'}
                                <!-- <span class="font-size-12 has-text-grey">2 minutos</span> -->
                                {#if file.loadStatus === 'is_loading'}
                                    <LoaderElement style_container={"max-width: 23px; max-height: 23px; width: 100%;"} style_loader={"font-size: 23px !important; padding: 0px !important;"} showMessage={false} />
                                    {:else}
                                    <span class="has-background-success flex-center" style="border-radius: 50%; width: 24px; height: 24px;">
                                        <i class="icon-tripod-checklist has-text-white font-size-10"></i>
                                    </span>
                                {/if}
                            {/if}
                            {#if file.loadStatus === 'is_failed'}
                                <span class="font-size-12 has-text-danger">
                                    No se pudo subir
                                </span>
                                <!-- svelte-ignore a11y-click-events-have-key-events -->
                                <i class="icon-tripod-repeat has-text-primary font-size-22 is-clickable" on:click={()=>{file.errorDetails.reload()}}></i>
                            {/if}
                        </div>
                    </div>
                {/each}
            </div>
        {/if}
    </div>
{/if}

<style>
.thumbnail-item{
    width: 156px;
    height: 88px;
    object-fit: cover;
    border-radius: 5px;
    overflow: hidden;
}
.thumbnail-item img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.files-container{
    border-radius: 12px;
    overflow: hidden;
    position: fixed;
    bottom: 2rem;
    right: 20px;
    min-width: 405px;
    /* border: 1px solid #E1E8ED; */
}
.file-title{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.arrow-drop{
    transform: rotate(0);
    transition: all .3s ease;
}
.arrow-drop.change{
    transform: rotate(180deg);
    transition: all .3s ease;
}
.progress{
    height: 10px;
    margin-bottom: 0;     
}
progress::-webkit-progress-value { 
    background-color: var(--color-primary);
    border-radius: 10px;
}
.stream-3pod-iframe{
    width: 100%;
    height: 100%;
    max-width: 1280px;
    max-height: 720px;
}
.title-editor-icons{
    position: absolute;
    right: 15px;
}
.active{
    border: 3px solid var(--color-primary);
}
@media only screen and (max-width:500px){
    .thumbnails-container{
        flex-wrap: wrap !important;
    }
    .thumbnail-item{
        width: calc(50% - 10px);
    }
}
</style>