<script>
import LinkPrimary from './../Buttons/LinkPrimary.svelte';
import {turpial} from './../../libraries/turpial';

/**
 * items to parse languages
 * on "self" value will be output original content
 */
export let items = [];


/**
 * set allowed crumbs to contain url link.
 */
export let available = [];

/**
 * 
 * go_back button 
*/
export let go_back = false;


/**
 * go back position, to go to previus view default value is 2.
 * if you want to go to a previus position, set a value higher to "2";
 */
export let go_back_position = 2;


/**
 * 
 * main website set
*/
export let main_website = true;




let controller = turpial.app.controller_name;
let Location = window.location.href.split("?")[0].split(controller)[1];
let breadcrumbs = Location?.split("/");
let paths = [];

let breadcrumbs_parsed = [];
breadcrumbs?.map((item)=>{
    if(item !== ""){
        breadcrumbs_parsed.push(item)
    }
})
breadcrumbs = breadcrumbs_parsed;

for (let index = 0; index < breadcrumbs.length; index++) {
    paths.push(breadcrumbs.slice(0, index+1));
}

paths = paths.map(( item )=>{
    item = `${controller}/${item.join("/")}`
    return item;
})
let back_slast = "";
if(controller === ("superuser" || "admin" || "user")){
    back_slast = "dashboard"
}

let previus = typeof paths.at(-(go_back_position)) === "undefined" ? `${controller}/${back_slast}` : paths.at(-(go_back_position));

</script>
<p class="is-flex gap-alt-7 pb-alt-4 font-size-14">
    {#if go_back !== false}
        <span>
            <LinkPrimary path="{previus}">
                <span class="is-underlined">
                    {go_back}
                </span>
            </LinkPrimary>
        </span>
        <span>
            |
        </span>
    {/if}
    {#if main_website}
        <span>
            <LinkPrimary path="">
                <span class="is-underlined">
                    Marketplace
                </span>
            </LinkPrimary>
        </span>
        <span>
            >
        </span>
    {/if}
    {#each breadcrumbs as breadcrumb, key}
        {#if items[key] !== null}
            <span>    
                {#if breadcrumbs.length === (key+1)}
                        {(items[key] === "self" ? breadcrumb : items[key]) ?? breadcrumb}
                    {:else}
                        {#if (available[key] ?? true) === false}
                            {(items[key] === "self" ? breadcrumb : items[key]) ?? breadcrumb}
                        {:else}
                            <LinkPrimary path="{paths[key]}">
                                <span class="is-underlined">
                                    {(items[key] === "self" ? breadcrumb : items[key]) ?? breadcrumb}
                                </span>
                            </LinkPrimary>
                        {/if}
                {/if}
            </span>  
            <span class="arrow">></span>    
        {/if}
    {/each}
</p>
<style>
.arrow:last-child{
    display: none;
}
</style>