class Core 
{
    constructor(data, updater){
        this.data = data;
        this.updater = ()=>{
            updater();
        }
        
    }
        /**
     * Function that filter the data according to a condition that is passed by parameter
     * @param {*} ARRAY this field correspond to the data where we apply the filter
     * @param {string} field field name or field index we want to spot
     * @param {string} condition  define which is the operator we want to use ejm: '===', '>', '<'
     * @param {*} filterData parameter that correspond to the data we want to filter
     */
    
    query(ARRAY, field, condition, filterData) // query("data", edad", ">=", 35)
    {
        let newData;

       switch(condition){ 
            case "==":
                newData = ARRAY.filter((value) => {
                    return value[field] == filterData;
                });
                console.log(newData);
            break;
            case "===":
                newData = ARRAY.filter((value) => {
                    return value[field] === filterData;
                });
                console.log(newData);
            break;
            case ">=":
                newData = ARRAY.filter((value) => {
                    return value[field] >= filterData;
                });
                console.log(newData);
            break;
            case "<=":
                newData = ARRAY.filter((value) => {
                    return value[field] <= filterData;
                });
                console.log(newData);
            break;
            case ">":
                newData = ARRAY.filter((value) => {
                    return value[field] > filterData;
                });
                console.log(newData);
            break;
            case "<":
                newData = ARRAY.filter((value) => {
                    return value[field] < filterData;
                });
                console.log(newData);
            break;
            case "in":
                newData = ARRAY.map((value) => {
                    let filter = value[field].search(filterData);
                    return filter !== -1 && value;
                }).filter((val) => val !== false);
                console.log(newData);
            break;
            case "not in":
                newData = ARRAY.map((value) => {
                    let filter = value[field].search(filterData);
                    return filter == -1 && value;
                }).filter((val) => val !== false);
                console.log(newData);
            break;

        }
        return newData;
        /* data = newData; */
        this.updater();
    }

}

export {Core}