<script>
import LoaderElement from './../../Others/LoaderElement.svelte';
import { turpial } from "../../../libraries/turpial";
import { onMount } from 'svelte';
import { clickOutside } from '../../../libraries/clickOutside.js';
export let is_loading = true;
export let id = "check-"+turpial.random_string(6);
export let name = "input_check"
export let value = "";
export let checkbox_value = false;
export let on_checked = function(){}
export let parent = null;
export let multipleSelector = true;
export let has_selector = true;

$: if(checkbox_value || !checkbox_value){
    if(multipleSelector){
        on_checked(checkbox_value);
    }
    else{
        // console.log(selectedCheck);
    }
}

let isShowed = false;
let menu_trigger;
let menu_list;
let container;
let selectedCheck;

function show_menu(state = 'toggle'){
    if(state === "toggle"){
        menu_trigger.classList.toggle('active');
    }
    else{
        menu_trigger.classList.remove('active');
    }
    isShowed = menu_trigger.classList.contains('active');

}

$:{
    if(isShowed && menu_list){
        container.addEventListener('mouseleave', e => {
            if(isShowed) {
                show_menu();
            }
        })
    }
}

onMount(() => {
    if(menu_list !== null){
        clickOutside([menu_list, menu_trigger], (destroy)=>{
            try {
                show_menu('remove')
            } catch (error) {
                destroy();
            }
        })
    }
})

function getSelected(target){
    let inputs = [...parent.querySelectorAll('input[type="checkbox"]')];
    inputs.forEach(element => {
        if (element.id != target.id) {
            element.checked = false;
        }
    });
}


</script>
<div class="input-check is-relative" data-is-loaded={is_loading} bind:this={container}>
    <!-- svelte-ignore a11y-mouse-events-have-key-events -->
    <div class="menu-video-trigger flex-center is-clickable" on:click={() => show_menu()} bind:this={menu_trigger}>
        <i class="icon-tripod-vertical-dots has-text-dark"></i>
    </div>
    <div class="menu-video-options {isShowed ? 'flex-column-flex-start' : 'is-hidden'} py-alt-0 px-alt-4" bind:this={menu_list}>
        <slot name="options"></slot>
    </div>
    <div class="full-size is-relative">
        {#if has_selector}
            <input type="checkbox" id={id} value={value} bind:checked={checkbox_value} bind:this={selectedCheck} name="{name}" class="input-checkbox formElements" style="position: absolute; top: 5px; left: 8px;" disabled={is_loading} on:click={(e)=>{
                getSelected(e.target)
            }}>   
        {/if}
        <div class="checkbox-container full-size is-flex justify-content-center align-items-flex-start">
            {#if is_loading }
                <LoaderElement style_container={"position: absolute; top: -17px;"} style_loader={"background: transparent;"} showMessage={false} />
            {/if}
            <div class="has-text-info is-unselectable font-size-14 fw500 full-size flex-center">
                <figure class="full-size flex-column">
                    <div class="multimedia__img full-width is-clickable" style="{is_loading ? 'opacity: 0.2; height: 100%;' : 'opacity: 1'}">
                        <slot name="img"></slot>
                    </div>
                    <figcaption class="full-size flex-center-flex-start p-alt-2 gap-15 fw500">
                        <slot name="title"></slot>      
                    </figcaption>
                </figure>              
            </div>
        </div>
    </div>
</div>
<style>
.input-check{
    max-width: 232px;
    width: 100%;
    max-height: 186px;
    height: 100%;
    position: relative;
}

.input-check[data-is-loaded='false']:hover .menu-video-trigger{
    display: flex;
}

.input-check::before{
    content: "";
    position: absolute;

}

.menu-video-trigger{
    position: absolute;
    top: 0px;
    right: 1px;
    width: 32px;
    height: 32px;
    background-color: rgba(255,255,255,0.5);
    z-index: 100;
    border-radius: 0px 8px;
    display: none;
}

.menu-video-options{
    position: absolute;
    width: 100%;
    height: fit-content;
    top: 30px;
    border-radius: 8px;
    background-color: #FFFFFF;
    z-index: 110;
    border: 1px solid var(--color-extralightGrey);
    line-height: 1.3;
}

.menu-video-options p:hover{
    background-color: #F5F8FA;
    border: 1px solid #F5F8FA;
    border-radius: 8px;
}

.checkbox-container{
    border: 1px solid var(--color-extralightGrey);
    border-radius: 8px;
    max-height: 186px;
    min-height: 186px;
}

.input-checkbox:checked:checked ~ .checkbox-container{
    border: 1px solid var(--color-primary);
    background-color: #296fff0f;
}

.multimedia__img{
    height: 130px;
    object-fit: cover;
}

.multimedia__img:global( img){
    height: 130px;
    border-radius: 8px 8px 0 0;
}

.checkbox-container:global( .button.is-loading::after){
    border-width: 8px !important;
}

</style>