import { writable } from 'svelte/store';

function supabase() {


	const { subscribe, update } = writable({
        /**
         * Initialize Supabase Client.
         */
        createClient: function(url, anon_key) {},
    });
	
	const data = {
        subscribe: subscribe,
        update: function(supabase){
            update(( data )=>{
                data = supabase;
                return data;
            })
        },
    }

    return data;
}


