import { writable } from 'svelte/store';

function customRoute() {


	const { subscribe, update } = writable({
        active: false,
		controller_name: "",
		action_name: null,
		parameters: [],
		path: "/",
		props: {},
	});
	
	const routes = {
        subscribe: subscribe,
        updateTitle: function( title ){
            update(( data )=>{
                data.title = title;
                return data;
            })

            return title;
        },
        update: function(path){
            path = path.split("/").filter(function(item){
                if(item !== ""){
                    return item;
                }
            }).join("/");
            

            let controller_name = path.split("/")[0] ?? "";

            

            let action_name = path.split("/")[1] ?? "";
            if(path === "/" || path === ""){
                controller_name = "index";
            }
            function getParameters(path){
                let parameters = path.split("/");



                if(parameters.length > 2){
                    parameters.shift();
                    parameters.shift();
                }else{
                    parameters = [];
                }
                return parameters;
            }



            let parameters = getParameters(path);
            let returns = {};
            update(function( data ){
                data.path = path;
                data.controller_name = controller_name;
                data.action_name = action_name;
                data.parameters = parameters;
                returns = data;
                return data;
            })


            return returns;
        },
        activate: function(){
            let app = this;
            update(function( data ){
                data.active = true;
                return data;
            })

            function setPath(){
                let path = window.location.hash.split("#path:")[1] ?? "/";
                app.update(path);
            }

            window.addEventListener("popstate", function(event){
                setPath();
            });

            setPath();
        },
        get: function(){
            let returns = {};
            update(function( data ){
                returns = data;
                return data;
            })
            return returns;
        }
    }

    return routes;
}


export const InlineRoute = customRoute();