import { compressImage } from "./compressImage";

let container = window?.document_root ?? document;

async function getThumbnailForVideo(videoUrl, time, callback) {
  const video = container.createElement("video");
  const canvas = container.createElement("canvas");
  video.style.display = "none";
  canvas.style.display = "none";

  // Trigger video load
  await new Promise((resolve, reject) => {
    video.addEventListener("loadedmetadata", () => {
      video.width = video.videoWidth;
      video.height = video.videoHeight;
      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;
      // Seek the video to 25%
      video.currentTime = video.duration * time;
      /**tiempo que dura el video en segundos */
    });
    video.addEventListener("seeked", () => resolve());
    video.src = videoUrl;
  });

  // Draw the thumbnailz
  canvas
    .getContext("2d")
    .drawImage(video, 0, 0, video.videoWidth, video.videoHeight);
  const imageUrl = canvas.toDataURL("image/jpeg");
  compressImage(canvas, 0.8, 0.8, callback);

  return {video};
}

export { getThumbnailForVideo };
