<script>
    import { RouterModuleStore } from "./RouterCases";


let module = null;

function get_component(){
    module = $RouterModuleStore.current_module.module
}

$: {
    if($RouterModuleStore){
        if($RouterModuleStore.render_on_toggle === true){
            get_component();
        }else{
            if(module === null){
                get_component();
            }
        }
    }
}

</script>
{#if module !== null}
    <svelte:component this={module} props={$RouterModuleStore.props} />
{/if}
