<script>
/**
 * @param status {String} options are: primary, success, warning, error
 */
export let status = "danger";
export let is_icon_align = false;
export let mainStyles = "padding: 0 !important;";
if(status === "error"){
    status = "danger";
}

if(status === ""){
    status = "danger";
}

</script>
<section class="notification-box {status} is-flex is-align-items-center p-alt-4 gap-alt-6" aria-hidden="true">
    <header class={is_icon_align ? 'is-align-self-flex-start' : ''}>
        <figure>
            <i class="icon-tripod-alert-outlined has-text-{status} font-size-40"></i>
        </figure>
    </header>
    <main style="{mainStyles}">
        <h3 class="fw600 font-size-14">
            <slot name="title"></slot>
        </h3>
        <p class="font-size-14">
            <slot></slot>
        </p>
    </main>
</section>
<style>
.notification-box{
    width: 100%;
    min-height: 90px;
    border-width: 1px; 
    border-style: solid;
    border-radius: 8px;
}
.notification-box:global(.danger), .notification-box:global(.error){
    border-color: var(--color-error);
    background-color: #fff0f0;
}
.notification-box:global(.warning){
    border-color: var(--color-warning);
    background-color: #fffef0;
}
.notification-box:global(.primary){
    border-color: var(--color-primary);
    background-color: #f2f7ff;
}
.notification-box:global(.success){
    border-color: var(--color-success);
    background-color: #e8f3ef;
}
</style>