<script>
import { page_parameters } from './../../parameters.route.store.js';
import { page } from '../../turpial.route.store';

import LinkPrimary from './../Buttons/LinkPrimary.svelte';
import { FetchApp } from '../functions/FetchApp.js';
export let paginatorData = {};

let show_numbers = true;

/**
 * @type {FetchApp}
 */
let fetch_app = paginatorData.get().fetch_app;

let {parameters} = paginatorData.get();
let total_items = paginatorData.get().total_items ?? 0;
let current_position, previus_position, next_position;



if(typeof paginatorData.get === "function"){
    show_numbers = true;
}

$: {
    if(paginatorData.get()){
        total_items = paginatorData.get().total_items ?? 0;
        current_position = paginatorData.get().parameters.page ?? 1;
        previus_position = (current_position - 1) <= 1 ? 1 : (current_position - 1);
        next_position = (current_position + 1) >= total_items ? total_items : (current_position + 1);
    }
}

function go_to( position_set ){
    fetch_app.execute_method((app)=>{
        
        if(current_position === position_set){
            return;
        }
        if(position_set > fetch_app.last_page){
            return;
        }
        
        if(typeof position_set === "number"){
            current_position = position_set;
        }

        paginatorData.update(( paginator )=>{
            paginator.fetch_app.position_update(current_position);

            paginator.parameters.page = current_position;
            return paginator;
        })

        
        paginatorData.get().submit();
        page_parameters.set({
            page: current_position
        })
    })
    
}

const set_page = (page)=> `?page=${page}`;
const get_last = ()=> Number.parseInt(Math.round((fetch_app.items ?? 1) / parameters.perpage));
</script>
{#if (fetch_app?.items ?? 0) > 0 && show_numbers === true}
    <navigation-numbers role="navigation" class="flex-center-flex-end gap-13 font-size-14 navigation-container">
        {#if current_position > 1}
            <a href="{set_page(previus_position)}" class="next-handdler-container flex-center gap-17" on:click|preventDefault="{()=>{ go_to(previus_position) }}">
                <i class="icon-tripod-arrow-left"></i>
                <p>Anterior</p>
            </a>
        {/if}
        <numbers-navigation class="is-flex gap-18">
            <a href="{set_page(1)}" data-type="first-element" class="{(current_position) === (1) ? "active": ""}" on:click|preventDefault="{()=>{ go_to(1) }}">
                1
            </a>
            {#if (current_position) > 2}
                <span class="has-text-dark">
                    ...
                </span>
            {/if}
            {#each paginatorData.get().fetch_app.navigation as item}                
                {#if (+item) > 1}
                    <a href="{set_page(item)}" data-type="list-element" class="{(current_position) === (item) ? "active": ""}" on:click|preventDefault="{()=>{ go_to(item) }}">
                        {item}
                    </a>
                {/if}
            {/each}
            {#if ((paginatorData.get().fetch_app.navigation).at(-1) !== get_last()) && get_last() > 2}
                <span class="has-text-dark">
                    ...
                </span>
                <a href="{set_page(get_last())}" data-type="last-element" class="{(current_position) === (get_last()) ? "active": ""}" on:click|preventDefault="{()=>{ go_to(get_last()) }}">
                    {get_last()}
                </a>
            {/if}
        </numbers-navigation>
        <a href="{set_page(next_position)}" class="next-handdler-container flex-center gap-17" on:click|preventDefault="{()=>{ go_to(next_position) }}">
            <p>Siguiente</p>
            <i class="icon-tripod-arrow-right"></i>
        </a>
    </navigation-numbers>
{/if}
<style>
.navigation-container :global(a){
    font-size: 1rem;
    padding: 0.344rem 0.75rem;
}
.navigation-container :global(.active){
    background: var(--color-primary);
    border-radius: 8px;
    color: #fff;
}
numbers-navigation{
    align-items: center;
}
a{
    text-decoration: none !important;
}
</style>