import { Core } from "./CustomStore.core";

/**
 * CustomStore allows us to transfer and update data passed by props in nested components.
 * When instantiating the class, we must pass the data we want to use and a function as an argument.
 * EXAMPLE:
        let testData;
        testData = new CustomStore( 
            {
                nameField: "John",
                lastNameField: "Doe"
            },
            function(){
                testData = testData;
            }
        );
 * the function must execute an assignment as shown in the example.
 */

class CustomStore extends Core {  
    #before_update_funcs = [];
    #on_update_funcs = [];

    constructor(data, updater){
        super();
        let app = this;
        this.data = data;


        /**
         * 
         * @param {function} fn 
         */
        this.on_update = (fn)=>{
            app.#on_update_funcs.push(fn);
        };


        /**
         * 
         * @param {function} fn 
         */
        this.before_update = (fn)=>{
            app.#before_update_funcs.push(fn);
        };        
        this.updater = ()=>{
            // before updating
            app.#before_update_funcs.map((fn)=>{
                if(typeof fn === "function"){
                    fn(this.data);
                }
            })

            // updating
            updater();


            // after updating
            app.#on_update_funcs.map((fn)=>{
                if(typeof fn === "function"){
                    fn(this.data);
                }
            })
        }
    }

    /**
     * controller() : is a class method that can behave in different ways depending on the parameters that we give it.
     * @param {string, number} field : its function is define what specific field of data we need to update.
     * @param {*} value : it can be an specific value of data field or complete value to be assigned to data or a variable.
     * @param {string} type : "all" to affect all data, "json" to work with json fields json and when data is json type, "array" to work with array fields when data is json type.
     * @param {string} method : define how it work, methods are "push", "update" or "delete".
     */
    #controller( field = null, value, type = "all", method){
        switch (type) {
            case "all":
                    if (method === "update") {
                        this.data = value;
                    }
                break;

            case "json":
                    try {
                        if (field != null) {
                            if (method === "update" || method === "push") {
                                this.data[`${field}`] = value;
                            } else if (method === "delete"){
                                delete this.data[`${field}`];
                            }
                        }
                    } catch (error) {
                        console.error(error);
                    }
                break;

            case "array":
                try {
                    if (method === "update") {
                        if (typeof field === "string") {
                            this.data[this.data.indexOf(`${field}`)] = value;
                        } else if (typeof field === "number"){
                            this.data[field] = value;
                        }
                    } else if (method === "push"){
                        this.data.push(value);
                    }else if (method === "delete"){
                        if (typeof field === "string") {
                            this.data.splice(this.data.indexOf(field), 1);
                        } else {
                            this.data.splice(field, 1);
                        }
                    }
                } catch (error) {
                    console.error(error);
                }
                break;

            default:
                break;
        }
        this.updater();
    }
    /**
     * get() : we can get information form class instance.
     * @param {function} MODIFIER : modifier function before get.
     * @returns : data
     */
    get(MODIFIER){
        MODIFIER = MODIFIER || null;
        if(typeof MODIFIER === "function"){
            this.data = MODIFIER(this.data);
            this.updater();
            return this.data;
        }

        return this.data; 
    }

    //ALL TYPE METHODS

    /**
     * update() : It allows us to update the value of a variable or a complete piece of data, it works like an assignment.
     * @param {*} value : the new value we want to give to data or one of its fields
     * @param {function} MODIFIER : the new value we want to give to data or one of its fields
     */
    update(MODIFIER){
        let value = MODIFIER(this.data);
        
        this.data = value;
        this.updater();
    }

    //JSON TYPE METHODS

    /**
     * setJson() : It allows us to update the value of a field of an object of type json
     * @param {string} field : field name to be edited.
     * @param {*} value : the new value we want to give to data or one of its fields.
     */
    setJson(field, value){
        this.#controller(field, value, "json", "update");
    }
    /**
     * deleteJson() : it helps us to eliminate a field when data is a json type.
     * @param {string} field : field name to be remove.
     */
    deleteJson(field){
        this.#controller(field, null, "json", "delete");
    }

    //ARRAY TYPE METHODS

    /**
     * setArray() : It allows us to update the value of a field of an object of type array.
     * @param {string} field : field name or field index to be edited.
     * @param {*} value : the new value we want to give to data or one of its fields
     */
    setArray(field, value){
        this.#controller(field, value, "array", "update");
    }
    /**
     * deleteArray() : it allow us delete or remove as specific array item.
     * @param {string, number} field : field name or field index to be removed.
     */
    deleteArray(field){
        this.#controller(field, null, "array", "delete");
    }

    /**
     * 
     * @param {string} field gestiona los datos de data cuando se le pasa un objeto
     * @returns json/array/data->update/delete.
     */
    manage(field)
    {
        let app = this;
        if(app.data.constructor !== Object){
            console.info("data need to be an 'Object' to access to their attributes.")
        }

        /**
         * @returns Internal methods
         */
         return {
            query: (fieldQuery, operator, value)=>{
                let query = app.query(app.data[field], fieldQuery, operator, value);

                return {  
                    /**
                     * @param {function} MODIFIER callback that execute before a replacement
                     * @returns the result of the query but replace app.data.$field
                     */
                    replace: (MODIFIER)=>{
                        let new_query_values;
                        if(typeof MODIFIER === "function"){
                            app.data[field] = MODIFIER(query);
                        }else{
                            app.data[field] = query;
                        }

                        return app.data[field];
                    },

                    /**
                     * @returns the result of the query.
                     */
                    results: ()=>{
                        return query;
                    },
                }
            },
            get: (callback)=>{
                callback = callback || function(content){
                    return content;
                };
                app.data[field] = callback(app.data[field]);
                app.updater();
                return app.data[field]; 
            },
            update: ( content, callback )=>{
                callback = callback || function(content){
                    return content;
                }; 
                /**
                 * objecto o datos, 
                 */
                app.data[field] = callback( content )
                app.updater();
                return app.data[field];
            },
            delete: (content)=>{
                delete app.data[field];
                app.updater();
            },
        }
    }
}



export {CustomStore};
