
import index_route from './Routes/index_route.js';


import { page } from './turpial.route.store.js';
const url = ()=> turpial.app;
const base = url().controller_name;
const baseAction = `${url().controller_name}/${url().action_name}`;

/**
 * Now all routes are stored inside a folder to manage it easily.
 */
const Routes = {
	index: index_route( AppRoute() ),	
	errors: {
		type: "public",
		404: {
			_render: ()=> import("./views/errors/_404.svelte").then((r)=> r),
		}
	}
};

















function AppRoute()
{
	class AppRoute
	{
		constructor(){
			this.path = url();
		}
		on_parameter({parameter, Redirection, onTrue, onFalse})
		{
			Redirection = Redirection||function(){};
			onTrue = onTrue||function(){};
			onFalse = onFalse||function(){};
			if(typeof url().parameters[parameter] !== "undefined"){
				Redirection();
				return onTrue()
			}else{
				return onFalse()
			}
		}
		currentPath()
		{
			return `${baseAction}/${url().parameters.join("/")}`
		}
		parameters(results, params)
		{
			params = params||[];
			return results;
		}
	}
	
	return new AppRoute();
}

export default Routes;
