


import { CheckUserLogged } from "./api/users/CheckUserLogged";
import { UserApp } from "./api/users/user_app";
import Routes from "./routes";

// use _self to execute controller function

let conditions = {
    user: {
        _self: function(controller, action, path, route){
            let type = route[turpial.app.action_name]?.type ?? "public";
            if(UserApp.data === null && type === "private"){
                CheckUserLogged((data)=>{
                    if(data.status === "error"){
                        window.location.href = turpial.app.host+"user/login"
                    }
                    UserApp.set(data.response);
                }); 
            }
        },
    },
    superuser: {
        _self: function(controller, action, path, route){
            let type = route[turpial.app.action_name]?.type ?? "public";
            if(UserApp.data === null && type === "private"){
                CheckUserLogged((data)=>{
                    if(data.status === "error"){
                        window.location.href = turpial.app.host+"superuser/login"
                    }
                    UserApp.set(data.response);
                }); 
            }
        },
    }
}






















function RouterConditions(controller, action, path){
    controller = controller || null;
    action = action || null;
    path = path || null;
    let route;    
    try {
        route = Routes[controller][action];
        conditions[controller][action](controller, action, path, route);
    } catch (error) {
        //console.warn(error)
    }
    try {
        route = Routes[controller];
        conditions[controller]?._self(controller, action, path, route);
    } catch (error) {
        //console.warn(error)
    }
}

export default RouterConditions;