let listofThumbnails = [
    {
        url: "https://www.40defiebre.com/wp-content/uploads/2014/05/imagen_redessociales.png"
    },
    {
        url: "https://www.40defiebre.com/wp-content/uploads/2014/05/imagen_redessociales.png"
    },
    {
        url: "https://www.40defiebre.com/wp-content/uploads/2014/05/imagen_redessociales.png"
    }
];
