 */
let plan_limits = $ApplicationData.plan_limits_testing;



authentication_channel.authentication($ApplicationData.user.token1, $ApplicationData.user.token2, plan_limits, (item)=>{
    //video_uploader.getVideosFromChannel(item, getVideos);
    URLS = `stream/channel/${item.id}/videos`;
    chanelExist = true;
    initPaginator();
    firtsCharge = true;
});

let filesData = [];

function copy_link(uid){
    let link = `https://stream.3pod.io/stream/watch/${uid}`;

    /**
     * copiar
    */
    if(typeof document.execCommand === "function"){
        var input = document.createElement('input');
        input.setAttribute('value', link);
        document.body.appendChild(input);
        input.select();
        var result = document.execCommand('copy');
        document.body.removeChild(input);
    }else{
        navigator.clipboard.writeText(link);
    }

    toast("¡Enlace copiado!", "success", 3000, "bottom-right");
}

/**
 * @function
 * @name uploadVideoController 
 * @param {object} e - evento de cambio de estado en el input de archivos.
 */

async function uploadVideoController(e){
    /**
     * files - lista de archivos a subir.
     */
    let files = e.target.files;
    let processinFiles = [];
    let videosData = [];

    for (let file of files) {
        let videoDataModel = {
            thumbnailImages: [],
            thumbnailsData : [],
            videoInfo: {}
        }
        let videoInfoModel = {
            thumbnails_group: [],
            thumbnail: "",
            video_duration: 0,
            title: "",
            file: {}
        }
        let copy = file;
        copy.title = file.name;
        copy.stackId = turpial.random_string(5);
        copy.loadStatus = "is_loading";
        copy.percentage = 5;
        copy.update_title = false;
        copy.update_thumbnails = false;
        copy.thumbnails_group = ["1", "2", "3"];
        copy.is_thumbnail_ready = false;
        /**
         * msg: mensaje de error,
         * type: prepareUpdoadVideo, uploadVideo, uploadVideo-event, parseThumbnails, uploadDefaultThumbnails
         */
        copy.errorDetails = {
            msg: "",
            type: ""
        }
        let fileUrl = URL.createObjectURL(file);
        ChannelData.addProcess(copy);
        processinFiles = $ChannelData.proccesing_files;

        let times = [0.25, 0.45, 0.75];
        for (let time of times) {
            let data = await getThumbnailForVideo(fileUrl, time, (blob, img)=>{
                videoDataModel.thumbnailImages.push(img);
                videoDataModel.thumbnailsData.push(blob);
                if(videoDataModel.thumbnailImages.length >=3 ){
                    completeVideoDataModel()
                }
            });
            videoInfoModel.video_duration = data.video.duration;
        }
        async function completeVideoDataModel(){
            videoInfoModel.title = file.name;
            videoInfoModel.file = file;
            videoInfoModel.thumbnail = videoDataModel.thumbnailImages[0];
            videoInfoModel.thumbnails_group = videoDataModel.thumbnailImages;
            videoInfoModel.thumbnails_blobs = videoDataModel.thumbnailsData;
            videoInfoModel.is_thumbnail_ready = true;
            videoInfoModel.update_title = false;
            videoInfoModel.update_thumbnails = false;
            videoInfoModel.errorDetails = copy.errorDetails;
            videoDataModel.videoInfo = videoInfoModel;
            videoDataModel.id = copy.stackId;
            videosData.push(videoDataModel);

            videosData = videosData;
            if (videosData.length === files.length) {
                ChannelData.update('stack', videosData);
                for (let video of videosData) {
                    InitVideoUpload(video.videoInfo, paginatorData);
                }
            }
        }
    }
}


let type_view = "empty_folder"; // empty_results, empty_folder


/**
 * paginator
 */
 $:{
    if (URLS && firtsCharge === true) {
        URLS = URLS;
        firtsCharge = false;
    }
}

function initPaginator(params) {
    fetch_app = new FetchApp(URLS);
    fetch_app.api_host = ApplicationData.get().api_host;
    fetch_app.updater = ()=>{ paginatorData = paginatorData }
    fetch_app.paginator = ()=> paginatorData;

    paginatorData = new CustomStore({
        fetch_app: fetch_app,
        updater: ()=>{ fetch_app.updater() },
        parameters: {
            perpage: 16,
            page: 1,
            sortby: "desc",
            orderby : "created_at",
            search: ""
        },
        submit: (e)=> {
            fetch_app.send(( url )=>{
                axios.get( url, {headers: {
                    "X-Token": $ChannelData.channel_token
                }} ).then(( response )=>{
                    totalItems = response.data.items;
                    itemsPerPage = paginatorData.data.parameters.perpage;
                    last_page = response.data.last_page;
                    if (response.data.current_items < paginatorData.data.parameters.perpage) {
                        current_items = response.data.current_items;
                    }else{
                        current_items = 0;
                    }
                    current_page = paginatorData.data.parameters.page;
                    filesData = response.data.data;
                    let reservedSize = $ChannelData.plan_limits.max_storage;
                    let reservedSize_bytes = gbTo(reservedSize, "bytes");
                    
                    
                    usedSize = bytesTo(response.data.total_size, "gb")
                    total_size = ((response.data.total_size * 100) / reservedSize_bytes);


                    // para parar los elementos de representación de cargas.
                    fetch_app = fetch_app.set_loaded( response.data );
    
                }).catch((error)=>{
                    console.error(error)
                    // para parar los elementos de representación de cargas. y generar un error a mostrar.
                    fetch_app = fetch_app.set_error();
                })
            })
        },
    }, ()=>{ fetch_app.updater() })
    paginatorData.get().submit();
}
/**
 * Pass properties to folder controller
*/
FolderController.set_property("paginator", paginatorData);
FolderController.set_property("fetch_app", fetch_app);

function openNameEditor(){
    stateEditor = true;
}
function closeNameEditor(){
    stateEditor = false;
}

function updateVideo(uid, type, src = false){
    if (type === "title") {
        if (videoName.length > 0) {
            video_uploader.updateVideo(uid, {title: videoName}, (data) => {
                paginatorData.get().submit();
            });
            stateEditor = false;
        } else {
            videoName = "";
            stateEditor = false;
            return;
        }
    }
    if (type === "thumbnail" && src) {
        isPosterResponse = true;
        video_uploader.updateVideo(uid, {thumbnail: src}, (data) => {
            filesData.map((item, key)=>{
                if (item.uid === uid) {
                    item.thumbnail = data.data.thumbnail;
                }
            })
            filesData = filesData;
            isPosterResponse = false;
        })
    }
}
function updatePoster(file){
    filesData.map((item, key)=>{
        if (item.uid === file.uid) {
            item.thumbnail = file.thumbnail;
        }
    })
    filesData = filesData;
}
function getItemsFromUpload(data){
    filesData = data
    filesData = filesData;
}


async function uploadCustomThumbnail(e, selected_video = false, is_upload = false){
    let file = e.target.files[0];
    
    let promiseData = await imgToCanvas(file);
    compressImage(promiseData, 0.8, 0.8, (blob, url)=>{
        let base64 = URL.createObjectURL(blob);
        if(selected_video.uid !== null){
            let thumbnail_controller = new ThumbnailController();
            thumbnail_controller.uploadCustomThumbnail(selected_video.uid, blob, (param)=>{
                paginatorData.get().submit();
            }, is_upload);
        }
    });
}

function copyIframe(uid){
    let link = `<iframe src="https://stream.3pod.io/stream/watch/${uid}"
                    frameborder="0"
                    style="border: none;min-height:400px;min-width:320px;width:100%;height:100%;"
                    allow="accelerometer; gyroscope; autoplay; encrypted-media; picture-in-picture;"
                    allowfullscreen="true"
                    title="3Pod video"
                ></iframe>`;
    navigator.clipboard.writeText(link);
    toast("¡Enlace copiado!", "success", 3000, "bottom-right");
}

