                                                                                                        {:else}
                                                                                                            <div class="thumbnail-item is-relative {file.thumbnail.split('?time')[0] === file.custom_thumbnail ? 'active' : ''}">
                                                                                                                <button class="is-clickable" type="button" style="position: absolute; top: 0; right: 0; border-radius: 0px 8px; opacity: 0.42;" on:click={()=>{
                                                                                                                    changeCustomThumbnail = !changeCustomThumbnail;
                                                                                                                }}>
                                                                                                                    <i class="font-size-16 icon-tripod-times has-text-danger"></i>
                                                                                                                </button>
                                                                                                                {#if changeCustomThumbnail}
                                                                                                                    <!-- content here -->
                                                                                                                    <div class="has-background-white p-alt-2" style="position: absolute; top: 32px; right: 0; border-radius: 8px; border: 1px solid #E1E8ED;">
                                                                                                                        <p class="fw500 has-text-grey">Reemplazar</p>
                                                                                                                        <input type="file" class="file-input formElements" accept=".jpg, .jpeg" name="bns" on:change={(e) => uploadCustomThumbnail(e, file)}>
                                                                                                                    </div>
                                                                                                                {/if}
                                                                                                                <img src="{file.custom_thumbnail + "?time=" + turpial.random_string(5)}" alt="thumbnail" class="is-clickable" on:click={(e)=>{updateVideo(file.uid, "thumbnail", e.target.src)}} style="{isPosterResponse ? 'pointer-events: none !important;' : ''}">
                                                                                                            </div>
                                                                                                        {/if}
                                                                                                    </div>
                                                                                                </div>
                                                                                            </div>
                                                                                        </div>
                                                                                    </div>
                                                                                </svelte:fragment>
                                                                                <div slot="trigger" let:active on:click={() => updatePoster(file)}>
                                                                                    <img data-source src={file?.thumbnail} class="full-size" style="object-fit: cover;" alt="{file?.long_name ?? ""}">
                                                                                </div>
                                                                            </SimpleModal>
                                                                        </svelte:fragment>
                                                                        <svelte:fragment slot="options">
                                                                            <p class="is-unselectable fw500 option has-text-grey" on:click={()=>{copy_link(file.uid)}}>Copiar link</p>
                                                                            <SimpleModal classes="has-responsive-height" externStyles={"width: 70%; max-width: 600px; height: fit-content !important;"} closeOnClickOverlay={true}>
                                                                                <svelte:fragment slot="header">
                                                                                    <div class="full-width flex-column gap-13">
                                                                                        <h2 class="font-size-24 fw700">Incorporar video</h2>
                                                                                    </div>
                                                                                </svelte:fragment>
                                                                                <svelte:fragment slot="content" let:modal let:hide>
                                                                                    <div class="has-element-rounded has-background-light-grey p-alt-4">
                                                                                        <code class="has-text-grey font-size-14 fw500" style="background-color: transparent; word-break: break-all !important;">
                                                                                            &lt;iframe
                                                                                            src="https://stream.3pod.io/stream/watch/{file.uid}"
                                                                                            frameborder="0"
                                                                                            style="border: none;min-height:400px;min-width:320px;width:100%;height:100%;"
                                                                                            allow="accelerometer; gyroscope; autoplay; encrypted-media; picture-in-picture;"
                                                                                            allowfullscreen="true"
                                                                                            title="3Pod video"&gt;&lt;/iframe&gt;
                                                                                        </code>
                                                                                    </div>
                                                                                </svelte:fragment>
                                                                                <svelte:fragment slot="footer">
                                                                                    <div class="full-width flex-center-flex-end">
                                                                                        <button class="button is-primary" on:click={()=>{copyIframe(file.uid)}}>Copiar</button>
                                                                                    <div>
                                                                                </svelte:fragment>
                                                                                <div slot="trigger" let:active>
                                                                                    <p class="is-unselectable fw500 option has-text-grey">Incorporar video</p>
                                                                                </div>
                                                                            </SimpleModal>
                                                                            <p class="is-unselectable fw500 option has-text-grey" on:click={() => video_uploader.deleteVideo(file.uid, ()=>{paginatorData.get().submit()})}>Eliminar Video</p>
                                                                            {#if file.on_r2}
                                                                                <p class="is-unselectable fw500 option has-text-grey" on:click={()=>{video_uploader.downloadVideo(file.uid)}}>Descargar</p>
                                                                            {/if}
                                                                        </svelte:fragment>
                                                                        <svelte:fragment slot="title">
                                                                            <div class="full-width flex-center-space-between">
                                                                                <span class="has-text-grey elipsis-multiple fw500" data-long-name={file?.long_name ?? ""}>{file.title}</span>
                                                                                <div class="button has-tooltip-top p-0 is-borderless full-height" data-tooltip="Copiar enlace">
                                                                                    <i on:click={() => copy_link(file.uid)} class="icon-tripod-link-alt has-text-dark is-clickable font-restricted-size-24" style={file?.loading  && 'visibility: hidden;'}></i>
                                                                                </div>                                          
                                                                            </div>
                                                                        </svelte:fragment>
                                                                    </CustomCheckBox>
                                                                    {/if}
                                                                {/each}
                                                            </media-grid>
                                                        </div>
                                                    {/if}
                                                    {:else}
                                                    <div class="is-justify-content-center is-flex is-align-items-center" style="height: 100%; min-height: inherit;">
                                                        <LoaderElement message="Cargando lista..." has_errors={fetch_app.has_errors} loaded={fetch_app.loaded} style_container={"position: absolute; top: 0; right: 0; bottom: 0; left: 0; margin: auto;"} />
                                                        <ErrorLoadingList has_errors={fetch_app.has_errors} on_reload={()=>{ paginatorData.get().submit() }} />
                                                    </div>
                                                {/if}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="full-width flex-center-flex-end">
                                    <div class="paginator-numbers">
                                        <PaginatorNumbers paginatorData={paginatorData} />
                                    </div> 
                                </div>
                            </div>
                        </div>
                    </main>
            {/if}
        </article>
        <FilesLoader externalItems={filesData} updateOrigin={getItemsFromUpload} paginator={paginatorData}></FilesLoader>
    </section>
{/if}

<style>
.custom-h1-sizing{
    font-size: 1.5rem;
}
.custom-icon-sizing{
    font-size: 1.875rem;
}
.title-editor-icons{
    position: absolute;
    right: 15px;
}
.thumbnail-item{
    width: 156px;
    height: 88px;
    object-fit: cover;
    border-radius: 5px;
    overflow: hidden;
}
.thumbnail-item img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.option{
    width: 100%;
    padding: 0.3125rem 0.3125rem;
    cursor: pointer;
    border-radius: 8px;
}
.option:hover{
    background-color: #F5F8FA;
}
.stream-3pod-iframe{
    width: 100%;
    height: 100%;
    max-width: 1280px;
    max-height: 720px;
}
input:global(.input){
    font-size: inherit;
}
.is-box-section{
    height: fit-content;
    position: relative;
    min-height: calc(70vh);
}

.progress{
    height: 10px;
    margin-bottom: 0;     
}

progress.primary-color::-webkit-progress-value { 
    background-color: var(--color-primary);
    border-radius: 10px;
}

progress.full-storage-color::-webkit-progress-value{
    background-color: var(--color-danger) !important;
    border-radius: 10px;
}
[disabled]{
    background-color: #B4C0C9;
}

input[type="file"]::-webkit-file-upload-button{
    width: 100%;
    cursor: pointer;
}

.elipsis-multiple{
    overflow:hidden; 
    text-overflow:ellipsis;
    display:-webkit-box; 
    -webkit-box-orient:vertical;
    -webkit-line-clamp:2;
    max-width: 203px;
}

.active{
    border: 3px solid var(--color-primary);
}
.left-column{
    width: 26% !important;
    max-width: 347px;
    min-height: inherit;
}
.right-column{
    width: 74% !important;
}
:global(article:has(.max-width-container)){
    max-width: 1440px;
}
media-grid{
    align-content: flex-start;
}
.media-grid:global( .input-check){
    width: 23% !important;
    min-width: 183px !important;
    max-width: 230px !important;
}

@media only screen and (max-width:1600px){
    :global(article:has(.max-width-container)){
        max-width: 1200px;
    }
    .media-grid:global( .input-check){
        width: 31.5% !important;
        min-width: 183px !important;
        max-width: 258px !important;
    }
    .custom-h1-sizing{
        font-size: 1.25rem
    }
}
@media only screen and (max-width:1440px){
    :global(article:has(.max-width-container)){
        max-width: 1080px;
    }
    .media-grid:global( .input-check){
        width: 31% !important;
    }
    .custom-h1-sizing{
        font-size: 1.125rem;
    }
    .custom-icon-sizing{
        font-size: 1.5rem;
    }
}
@media only screen and (max-width:1366px){
    :global(article:has(.max-width-container)){
        max-width: 1024px;
    }
    .media-grid:global( .input-check){
        width: 31% !important;
        max-width: 281px !important;
    }
    .custom-h1-sizing{
        font-size: 1rem;
    }
    .custom-icon-sizing{
        font-size: 1.375rem;
    }
}
@media only screen and (max-width:1200px){
    :global(article:has(.max-width-container)){
        max-width: 900px;
    }
    .media-grid:global( .input-check){
        width: 47% !important;
    }
    .left-column{
        width: 29% !important;
        max-width: 347px;
    }
    .right-column{
        width: 71% !important;
    }
}
@media only screen and (max-width:1024px){
    :global(article:has(.max-width-container)){
        max-width: 760px;
    }
    .left-column{
        width: 35% !important;
        max-width: 239px;
    }
    .right-column{
        width: 61% !important;
    }
    .media-grid:global( .input-check){
        width: 47% !important;
    }
}
@media only screen and (max-width:900px){
    .right-column{
        min-width: 320px;
        max-width: 100%;
    }
}
@media only screen and (max-width:500px){
    .thumbnails-container{
        flex-wrap: wrap !important;
    }
    .thumbnail-item{
        width: calc(50% - 10px);
    }
}

</style>