<script>
export let classes = "has-not-decoration is-primary";
export let id = "";
export let style = "";
export let path = "";
export let to = null;
export let on_click = function(){};
export let static_element = false;

import { page } from '../../turpial.route.store.js';
if(typeof to === "string" || typeof to === "number"){
	let current = $page.path;
	current = current.slice(-1) === "/" ? current+"" : current+"/";
	path = current+`${to}`
}

function route(){
	if(static_element){return;}
	page.route(path);
}

</script>
<a 
	on:click|preventDefault={on_click} 
	on:click|preventDefault={()=>{ route(); }}
	class="{classes}" 
	style="{style}" 
	id="{id}" 
	href="{page.getPath(path)}" 
>
	<slot></slot>
</a>