import { writable } from 'svelte/store';
import { turpial } from './libraries/turpial';

function ParameterSet() {


    function get_params(url_params){
        let params = {};
        for (const [key, value] of url_params.entries()) {
            params[key] = value;
        } 
        return params;
    }


	const { subscribe, update } = writable({
        parameters: {},
        params_string: "",
    });
	
	const parameter_set = {
		subscribe,
        set: function(props){
            props = props||{};
            let params = new URLSearchParams(window.location.search);
            Object.values(props).map((value, key)=>{
                let name = Object.keys(props)[key];

                params.set(name, value);
            })

            let toObject = get_params(params);
            console.log(params)
            let page = `${window.location.href.split("?")[0]}?${params.toString()}`;




            window.history.pushState(toObject, document.title, page);
        }
	}
	
	return parameter_set;
}


export const page_parameters = ParameterSet();