import { VideoController } from "./VideoController";
import { toast } from './../../../libraries/toast.js';
import { ChannelData } from "../ChannelData.store";

function InitVideoUpload(info, paginator){
    if (info.thumbnails_group.length >= 3 && info.thumbnails_group[0] != "1") {
        info.is_thumbnail_ready = true;
        ChannelData.thumbnailsGroupUpdater(info.file.stackId, info.thumbnails_group, true);
    }else{
        //error no pudo conseguir 3 thumbnails
        return;
    }
/*     if(Math.ceil(info.video_duration) > 300){
        toast("Solo puedes subir vídeos con un máximo de 5min", "warning", 3000, "bottom-right");
        //error duracion y eliminar item del stack y de processing
        ChannelData.deleteItemById("proccesing_files", info.file.stackId, true);
        ChannelData.deleteItemById("stack", info.file.stackId, true);
        return;
    } */
    if(info.title.length > 600){
        toast("Solo puedes subir vídeos con un titulo de maximo 600 caracteres", "warning", 3000, "bottom-right");
        //error titulo del video y eliminar item del stack y de processing
        ChannelData.deleteItemById("proccesing_files", info.file.stackId, true);
        ChannelData.deleteItemById("stack", info.file.stackId, true);
        return;
    }
    if(info.title.length <= 600){
        let video_uploader = new VideoController(info.file.stackId, paginator);
        video_uploader.prepareUploadVideo(info);
    }
}
export { InitVideoUpload }