import { writable } from 'svelte/store';
import { UserType } from '../../models/UserProfile.model';

let user = window

function folderController() {
	const { subscribe, set, update } = writable({
        selected_files: [],
        folders: [],
        folder_selected: "",
        input_creator_open: null,
        fetch_app: {},
        paginator: {},
        select_main: function(){},
    });
	return {
        subscribe,
        set_property(property, value)
        {
            update((data)=>{
                data[property] = value;
                return data;
            });
        },
        restart()
        {
            update((data)=>{
                data.selected_files = [];
                data.folder_selected = "";
                data.input_creator_open = null;

                return data;
            });
        },
        set_folders(folders){
            update((data)=>{
                data.folders = folders;
                return data;
            });
            return folders;
        },
        add_folder(folder){
            let folders = [];
            update((data)=>{
                data.folders.push(folder);
                folders = [...data.folders];
                return data;
            });
            return folders;
        },
        select: (folder_selected)=>{
            let app = this;
            update((data)=>{
                data.folder_selected = folder_selected

                let body = data.fetch_app.body;
                body.folder = folder_selected;
                data.fetch_app.set_body(body);
                data.paginator.get().submit();

                return data;
            })
        },
        /**
         * 
         * @param {HTMLElement} input 
         */
        open_creator: (input)=>{
            update((data)=>{
                data.input_creator_open = input
                return data;
            })
        },
    }
}


export const FolderController = folderController();