import { writable } from 'svelte/store';

function channelData() {

    const { subscribe, update } = writable({
        channel_data: {},
        channel_token: "",
        plan_limits: {},
        files: [],
        proccesing_files: [],
        stack: [],
        completed: 0
    });

    const StreamChannel = {
        subscribe: subscribe,
        update: function(field, new_data){
            update(( data )=>{
                data[field] = new_data;
                return data;
            })
        },
        get: function(){
            let returns = {};
            update(function( data ){
                returns = data;
                return data;
            })
            return returns;
        },
        addProcess: function(new_data){
            update(function( data ){
                data.proccesing_files.push(new_data);
                return data;
            })
        },
        addQueue: function(new_data){
            update(function( data ){
                data.stack.push(new_data);
                return data;
            })
        },
        deleteStackItem: function(id){
            update(function( data ){
                data.stack.map((item, key)=>{
                    if (item.id === id) {
                        data.stack.splice(key, 1);
                        return data;
                    }
                })
                return data;
            })
        },
        updateProcess: function(new_data, status, percentage, stackId){
            update(function( data ){
                let defaulObject;
                if (new_data === null) {
                    data.proccesing_files.map((item, key)=>{
                        if (data.proccesing_files[key].stackId === stackId) {
                            defaulObject = data.proccesing_files[key];
                        }
                    })
                }else{
                    defaulObject = new_data;
                }
                defaulObject.loadStatus = status;
                defaulObject.percentage += percentage;
                if (defaulObject.percentage >= 100) {
                    defaulObject.loadStatus = "is_completed";
                    data.completed += 1;
                }
                defaulObject.stackId = stackId;

                data.proccesing_files.map((item, key)=>{
                    if (data.proccesing_files[key].stackId === stackId) {
                        data.proccesing_files[key] = defaulObject;
                    }
                })
                return data;
            })
        },
        updateProcessThumbnailById: function(uid, thumbnail){
            update(function( data ){
                data.proccesing_files.map((item, key)=>{
                    if (item.uid === uid) {
                        item.thumbnail = thumbnail;
                        data.proccesing_files = data.proccesing_files;
                        return data;
                    }
                })
                return data;
            })
        },
        errorController: function(id, errorData, prepareUploadVideo = false){
            update(function( data ){
                data.proccesing_files.map((item, key)=>{
                    if (prepareUploadVideo) {
                        if (item.stackId === id) {
                            item.errorDetails = errorData;
                            item.loadStatus = "is_failed";
                            data.proccesing_files = data.proccesing_files;
                            return data;
                        }
                    } else {
                        if (item.uid === id) {
                            item.errorDetails = errorData;
                            item.loadStatus = "is_failed";
                            data.proccesing_files = data.proccesing_files;
                            return data;
                        }
                    }
                })
                return data;
            })
        },
        thumbnailsGroupUpdater: function(id, thumbnailsGroup, stackId = false){
            update(function( data ){
                data.proccesing_files.map((item, key)=>{
                    if (stackId) {
                        if (item.stackId === id) {
                            item.thumbnails_group = thumbnailsGroup;
                            item.is_thumbnail_ready = true;
                            return data;
                        }
                    } else {
                        if (item.uid === id) {
                            item.thumbnails_group = thumbnailsGroup;
                            item.is_thumbnail_ready = true;
                            return data;
                        }
                    }
                })
                return data;
            })
        },
        customThumbnailUpdater: function(uid, thumbnail){
            update(function( data ){
                data.proccesing_files.map((item, key)=>{
                    if (item.uid === uid) {
                        item.custom_thumbnail = thumbnail;
                        item.thumbnail= thumbnail;
                        return data;
                    }
                })
                return data;
            })
        },
        deleteItemById: function(field, id, stackId = false){
            update(function( data ){
                let result = data[field].filter((item)=>{
                    if (stackId) {
                        if (field === "stack") {
                            if (item.videoInfo.file.stackId !== id) {
                                return item;
                            }
                        }else{
                            if (item.stackId !== id) {
                                return item;
                            }
                        }
                    } else {
                        if (item.uid !== id) {
                            return item;
                        }
                    }
                })
                data[field] = result;
                return data;
            })
        }
    }
        return StreamChannel;
    }


export const ChannelData = channelData();