<script>
import InputSearchSkeleton from './InputSearchSkeleton.svelte';
export let paginatorData = {};
let input;
const update = () => {
    paginatorData.update((object)=>{
        object.parameters.search = input.value;
        return object; 
    })
    
}

function submit()
{
    paginatorData.data.fetch_app.execute_method(()=>{
        paginatorData.data.submit();
    })
}

</script>
<form action="POST" on:submit|preventDefault={submit} class="full-width">
    <InputSearchSkeleton>
        <input type="search" bind:this="{input}" class="input formsElements full-width" id="search" placeholder="Buscar" name="search" on:input={update}>
    </InputSearchSkeleton>
</form>