<script>
export let Class = "";
export let id = "";
export let style = "";
export let type = "primary-color";
export let on_click = function(){};
let classes = "button is-primary";

if(type === "content"){
	classes = "content";
}
</script>
<style>
button {
	cursor: pointer;
	text-decoration: none !important;
	border: 0;
    border-radius: 10px;
	transition: all 0.1s ease;
}
button.is-border {
	border: #1ba3ff solid 1px;
	color: linear-gradient(45deg, #277aff, #1ba3ff, #17b2ff);
}
button.is-border-white {
	border: white solid 1px;
	color: white !important;
}
button.is-rounded{
	border-radius: 40px;
}
button.is-rounded-all{
	border-radius: 100px !important;
}
button.primary-color {
	background: linear-gradient(45deg, #277aff, #1ba3ff, #17b2ff);
	color:#fff;
    box-shadow: inset 0 13px 27px rgb(27 59 98 / 25%);
}
button.light-color {
	background: #81D2FB;
	color:#fff;
    border-radius: 10px;
}
button.primary-color *{
	color:#fff;
}
button.primary-color:hover {
	transform: scale(1.02);
	transition: all 0.1s ease;
}

button.secundary-color {
	background-color: var(--color-secundary);
    color: var(--color-grey);
}
button.secundary-color *{
	color: var(--color-grey);
}
button.secundary-color:hover {
	color: var(--color-grey);
	transform: scale(1.02);
	transition: all 0.1s ease;
}

button.neutral-color {
	background-color: transparent;
    color: var(--color-primary);
}
button.neutral-color *{
	color: var(--color-primary);
}
button.neutral-color:hover {
	text-decoration: none !important;
	transition: all 0.1s ease;
}

button.success-color {
	background-color: var(--color-success);
    color: #ffffff;
}
button.success-color *{
	color: #ffffff;
}
button.success-color:hover {
	color: #ffffff;
	transform: scale(1.02);
	transition: all 0.1s ease;
}
button.content{
	background-color: transparent;
}
button.is-shadowless:focus, button.is-shadowless{
	box-shadow: none;
}
@media only screen and (max-width:465px){
	button.is-mobile-size{
		width: 120px;
	}
}
button.is-danger{
	background: var(--color-danger);
}
</style>
<button on:click={on_click} style="{style}" class="{Class} {type} {classes}" id="{id}">
	<slot></slot>
</button>