<script>
import { ApplicationData } from './data.application.route.js';
import { InlineRoute } from './inline.routes.js';
import Styles from './Styles.svelte';
import Router from './components/Router.svelte';
import {turpial} from './libraries/turpial';
import {user_testing} from './models/user_testing';
import {SupabaseEvents, Supabase, SupabaseData} from './libraries/Supabase/Supabase.store';
import { onMount } from 'svelte';
import { ShadowRoot } from 'tripod-styles/src/ShadowRoot.js';
import variable_styles from "tripod-styles/src/sass/variables.scss";

InlineRoute.activate();

let style = document.createElement("style");
style.innerText = variable_styles;
document.body.appendChild(style);

/**
 * props from outside
 */
let props = $$props;

let is_online = props.is_online ?? false;
let host = props.host ?? null;
let title = props.title ?? "Dashboard";
let enviroment = props.enviroment ?? null;

let user = props.user_tokens ?? user_testing;
let go_to_plans = props.go_to_plans ?? "#";

//is_online = "localhost";


ApplicationData.update("is_online", is_online);
ApplicationData.update("user", user);
ApplicationData.update("host", host);
ApplicationData.update("go_to_plans", go_to_plans);



if($ApplicationData.is_online === true){
	console.warn("production enviroment initiated")
	ApplicationData.update("app3pod_host", "https://app.3pod.io/public/api/v1/services/");
	ApplicationData.update("api_host", "https://stream.3pod.io/api/v1/services/");
}else if($ApplicationData.is_online === false){
	console.warn("localdev enviroment initiated")
	ApplicationData.update("app3pod_host", "https://dev.3pod.io/public/api/v1/services/");
	ApplicationData.update("api_host", "http://localhost/stream.3pod.io/api/v1/services/");
}


if($ApplicationData.is_online === false && enviroment === "dev"){
	console.warn("dev.3po.io enviroment initiated")
	ApplicationData.update("app3pod_host", "https://dev.3pod.io/public/api/v1/services/");
	ApplicationData.update("api_host", "https://stream.3pod.io/api/v1/services/");
}

if($ApplicationData.is_online === false && enviroment === "localhost"){
	console.warn("local with local app.3pod.io enviroment initiated")
	ApplicationData.update("app3pod_host", "http://localhost/app.3pod.io/public/api/v1/services/");
	ApplicationData.update("api_host", "http://localhost/stream.3pod.io/api/v1/services/");
}

let components_loaded = false;
try {
	turpial.fetch({
		url: ["https://cdn.jsdelivr.net/npm/@supabase/supabase-js", "https://cdn.jsdelivr.net/npm/js-cookie@3.0.1/dist/js.cookie.min.js"],
		ready: ()=>{
			
			Supabase.update(window.supabase);
			let supabase_client = $Supabase.createClient(SupabaseData.url, SupabaseData.public_key);

			
			window.SupabaseData = SupabaseData;
			
			Supabase.update(supabase_client);


			window.$supabase = supabase_client

			window.$supabase.removeChannel = function(channel){
				window.$supabase.realtime.channels.map(function (e) {
					if(e.topic === `realtime:${channel}`){
						e.unsubscribe();
					}
				})
			}
			
			$Supabase.removeAllChannels()
			console.log("Subscribing")
			/*setTimeout(() => {
				$Supabase
				.channel('public:countries:id=eq.200')
				.on('postgres_changes', { event: 'UPDATE', schema: 'public', table: 'countries', filter: 'id=eq.200' }, handleRecordUpdated)
				.subscribe()
			}, 100);*/

			components_loaded = true;
		},
	})
} catch (error) {
	
}

window.scroll_top = function(){
	window.scrollTo({ top: 0, behavior: 'smooth' });
}

let show = false;
let injected_styles = null;
let shadow = document.querySelector(".tripod-app .svelte-app-child").shadowRoot;
window.document_root = shadow;




$: {
	if(components_loaded === true && injected_styles !== null){
		let root_styles = document.querySelector("#injected-css-sv");
		injected_styles.appendChild(root_styles);
		show = true;
		
		ShadowRoot.update(shadow);
		//ShadowRoot.update(document);

		turpial.linkCSS({
			target: [
				document.head,
				$ShadowRoot.querySelector(".injected-css-sv-container")
			],
			url: [
				"https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css",
				"https://cdn.jsdelivr.net/gh/Yerikmiller/3pod-us.github.io@tpi-1.2.4/icons/platform/tripod-pack/tripod-pack.css",
				"https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;700;800&display=swap",
				"https://use.fontawesome.com/releases/v5.12.1/css/all.css",
			]
		})		
	}
}

$: console.log($ShadowRoot)

</script>
<svelte:head>
	<meta name="viewport" content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no" />
	<meta name="robots" content="index, follow">
	<meta name="charset" content="UTF-8">
	<title>{title}</title>
	<script>
		turpial = turpial;
		__HOST = __HOST;
	</script>
	<style>
		body, html{
			background-color: #f5f8fa;
			overflow-x: hidden !important;
			overflow-y: overlay !important;
		}
	</style>
</svelte:head>
{#if components_loaded}
	<!-- container to inject styles -->
	<div class="injected-css-sv-container" bind:this="{injected_styles}"></div>
	<Styles />
	{#if show}
		<Router CustomRoute={$InlineRoute}/>
	{/if}
{/if}

	