import { RouterModel, PropertiesRoutes } from '../models/Routes.model';
import { RouterStore } from '../RouterStore';
import { writable } from 'svelte/store';

class RouterCases 
{
    /**
     * 
     * @param {Object} structure 
     * @param {Boolean} return_layout 
     */
    constructor(structure, return_layout)
    {
        this.structure = structure;
        this.valid_view = true;
        this.router_class_started = false;
        this.parameters = {};
        this.valid_case = null;
        /**
         * Entorno de case.
         */
        this.enviroment = null;
        this.return_layout = return_layout;
        this.valid_views = 0;
        this.application = null;
        this.props = {};
        this.executer = function(){}
        this.catch_error = function(){}
    }
    /**
     * Create cases to execute functions
     * @param {String} path 
     * @param {CallableFunction} callback 
     */
	case(path, executer)
    {
        let app = this;

        let CASE = path;


        app.structure.view;
        
        let controller_action = [];
        let controller = this.structure.controller_name;
        let action = this.structure.action_name;
        if(controller !== ""){
            controller_action.push(controller);
        }
        if(action !== ""){
            controller_action.push(action);
        }

        controller_action = controller_action.join("/");
        let from = app.structure.view.split(controller_action)[1];


        path = path.split("/");
        from = from.split("/");

        path = path.filter(( item )=>{            
            if(item !== ""){
                return item;
            }
        })
        from = from.filter(( item )=>{
            if(item !== ""){
                return item;
            }
        })
        let valid_view = true;
        let parameters_pairs = {};
        let valid_views_routes = [];
        
        path.map(( item, key )=>{
            
            if(typeof from[key] !== "undefined"){
                let name = item.split("{").join("").split("}").join("");
                if(item.search("{") >= 0 && item.search("}") >= 0 && item !== ""){                    
                    parameters_pairs[name] = from[key];
                    valid_views_routes = path;                 
                    return;
                }
                
                if(item !== from[key]){
                    valid_view = false;
                    valid_views_routes = [];
                }else{
                    parameters_pairs[name] = from[key];
                    valid_views_routes = path;
                }
                
            }else{
                valid_views_routes = [];
                valid_view = false; 
            }
        })


        app.valid_view = valid_view;
        if(path.length !== from.length){
            return;
        }
        
        if(JSON.stringify(path) !== JSON.stringify(valid_views_routes)){
            valid_view = false;
            return;
        }
        
        if(valid_view === true){
            app.parameters = parameters_pairs;
            this.valid_views++;
            if(this.valid_views > 1){
                throw(`Existen dos casos o dos rutas: ${CASE}`);
            }
            this.valid_case = CASE;
            this.controller = controller_action;
            this.enviroment = app;
        }
        app.executer = executer;
    }
    /**
     * 
     * @param {RouterModel} app 
     * @returns 
     */
    start(app)
    {
        this.router_class_started = true;
        app._limit_params = false;
        
        
        this.application = app;
        if(this.valid_views === 0){
            console.error("No existen parámetros de URL válidos.");
            return null
        }

        if(this.valid_views === 1){
            app._valid_case = this.valid_case;
            app._controller = this.controller;
            app.render_on_toggle = app?.render_on_toggle ?? true;
            this.props = app.props;
            this.executer();
        }else{
            console.error("Posiblemente existen dos endpoints con la misma estructura de URL")
            return null
        }

        return app;
    }
    set_props(return_layout)
    {
        let app = this;
        RouterStore.set("parameters", this.parameters);
        RouterStore.set("controller_name", this.structure.controller_name);
        RouterStore.set("action_name", this.structure.action_name);
        
        
    }
}

function routerModuleStore(){

    const {update, subscribe} = writable({

        /**
         * @type {{name: {String}, module: {Object}}}
         */
        current_module: {
            name: null,
            module: null,
        },

        /**
         * @type {String}
         */
        current_controller: "/", 

        /**
         * @type {PropertiesRoutes}
         */
        props: {},

        /**
         * @type {Boolean}
         */
        render_on_toggle: true
    })


    let subscriber = {
        subscribe,
        /**
         * Set a module on current view
         * @param {String} module_name 
         * @param {Object} module 
         */
        set_module: (module_name, module)=>{
            update((data)=>{
                data.current_module.name = module_name;
                data.current_module.module = module;
                return data;
            })
        },
        /**
         * Set a controller for this instance.
         * @param {String} controller_name 
         */
        set_controller: (controller_name)=>{
            update((data)=>{
                data.current_controller = controller_name;
                return data;
            })
        },
        /**
         * 
         * @param {PropertiesRoutes} props 
         */
        set_props: (props)=>{
            update((data)=>{
                /**
                 * @type {PropertiesRoutes}
                 */
                data.props = props;
                return data;
            })
        },
        set_property: (property, value)=>{
            update((data)=>{
                data[property] = value;
                return data;
            })
        }
    }

    return subscriber;
}

/**
 * Set a current module on store.
 */
const RouterModuleStore = routerModuleStore();


export {RouterCases, RouterModuleStore}