<script>
export let Styles = "";
/**
 * versions:
 * is-small-rectangle -
 * is-medium-rectangle -
 * is-large-rectangle
 * @type {String}
 */
export let Classes = "is-medium-rectangle";
export let fileUploader = () => {};
export let textSizing = "";
export let imgStyles = "";

</script>


<div class="file-select {Classes} is-dashed" style={Styles}>
    <input type="file" class="file-input formElements" name="bns" on:change={(e) => fileUploader(e)}>
    <img src="https://i.postimg.cc/dVGyGDvP/3-Pod-Icons-medios-1.png" class="icon-file" alt="icon-file-3pod" style="{imgStyles}">
    <p class="has-text-centered fw500 {textSizing}" style="color: #CED5DC;">
        {#if !$$slots.default}
        Arrastra y suelta tus <br>
            <span class="has-text-primary {textSizing}">medios</span>
        {/if}
        <slot></slot>
    </p>
</div>
<style>
.file-select::before{
    background-color: transparent !important;
}
@media only screen and (max-width:900px){
    .file-select.has-small-responsive img{
        width: 25px !important;
        height: 25px !important;
    }
    .file-select.has-small-responsive p, .file-select.has-small-responsive span{
        font-size: 10px !important;
    }
}
</style>
