<script>
export let style_container = "";
export let style_loader = "";
export let has_errors = false;
export let loaded = false;
export let showMessage = true;
export let message = "Cargando componentes."
</script>
<style>
.button.is-loading:after{
    border-color: transparent transparent var(--color-primary) var(--color-primary);
}
</style>
{#if has_errors == false}
    {#if loaded == false}
        <div class="is-flex is-justify-content-center is- is-align-items-center" style="flex-direction: column;{style_container}">
            <button class="button is-loading is-outlined is-borderless is-large is-size-1" style="{style_loader}"></button>
            {#if showMessage}
                <p>
                    {message}
                </p>
            {/if}
        </div>
    {/if}
{/if}
