function secondsToString(data) {
    let seconds = Math.round(data);
    let minute = Math.floor((seconds / 60) % 60);
    minute = (minute < 10)? '0' + minute : minute;
    let second = seconds % 60;
    second = (second < 10)? '0' + second : second;
    return minute + ':' + second;
}

function bytesTo(number, typeTo) {
    let result
    switch (typeTo.toLowerCase()) {
        case "gb":
            result = number / 1000000000;
            return Number.parseFloat(result.toFixed(3));
        break;
        case "mb":
            result = number / 1000000;
            return Number.parseFloat(result.toFixed(3));
        break;
    }
}

function gbTo(number, typeTo) {
    let result
    switch (typeTo.toLowerCase()) {
        case "bytes":
            return Number.parseInt(number * 1000000000);
        break;
        case "mb":
            return Number.parseInt(number * 1000000);
        break;
    }
}

export { secondsToString, bytesTo, gbTo};