import {Topbar} from "../../libraries/Topbar"
import { ApplicationData } from "../../data.application.route";

class FetchApp 
{
    /**
     * Create a url REST API passing some arguments.
     * @param {String} url 
     */
    constructor( url )
    {
        this.path_url = url;
        this.loaded = false;
        this.is_loading = false;
        this.has_errors = false;
        this.api_host = ApplicationData.get().app3pod_host;
        this.parameters = {};
        this.last_page = 1;
        this.navigation = [];
        this.position = 1;
        this.items = 0;
        this.data = {};
        this.body = {};
        this.paginator = ()=>{};
        this.updater = ()=>{};

        return this.get_url();
    }
    set_body(properties, has_value)
    {
        has_value = has_value || "has.no.value.defined";
        if(has_value !== "has.no.value.defined"){

            let property = properties; // redefine.
            let value = has_value;
            this.body[property] = value;

            return this.body;
        }
        this.body = properties;
        return this.body;
    }
    set_parameters(parameters, has_value)
    {
        has_value = has_value || "has.no.value.defined";
        if(has_value !== "has.no.value.defined"){

            let parameter = parameters; // redefine.
            let value = has_value;
            this.parameters[parameter] = value;

            return this.parameters;
        }
        this.parameters = parameters;
    }
    get_url(parameters, host)
    {
        host = host ?? this.api_host;
        this.parameters = parameters || this.parameters;
        this.url_params = new URLSearchParams(this.parameters).toString();
        this.url = `${host}${this.path_url}?${this.url_params}`;
        return this.url;
    }
    set_url(url)
    {
        this.path_url = url;
        return this.get_url();
    }
    /**
     * set variables loaded and has_errors
     */
    set_loaded(response)
    {
        
        response = response || null;
        this.loaded = true;
        this.has_errors = false;
        let paginator = this.paginator();
        new Topbar().hide();
        this.is_loading = false;

        

        if(response !== null){
            this.navigation = response.navigation;
            this.data = response.data;
            this.items = response.items;
            this.last_page = response.last_page;
            paginator.update(function(paginator){
                paginator.total_items = response.items
                return paginator;
            })
        }
        
        this.updater();

        return this;
    }
    /**
     * set variables loaded and has_errors
     */
    set_error()
    {
        this.loaded = false;
        this.has_errors = true;
        this.is_loading = false;
        new Topbar().hide();
        return this;
    }
    /**
     * restart variables loaded and has_errors
     */
    restart()
    {
        this.loaded = false;
        this.has_errors = false;
        this.is_loading = false;
        new Topbar().hide();
        return this;
    }
    /**
     * loading status
    */
    loading()
    {
        this.is_loading = true;
        this.loaded = false;
        new Topbar().show();
        return this;
    }
    position_update(position)
    {
        this.position = position;
        return this;
    }
    /**
     * construct call and send.
     * @param {CallableFunction} callback 
     * @returns 
     */
    send(callback)
    {
        if(this.is_loading === true){
            return;
        }
        this.loading();
        callback(this.get_url(this.paginator()?.get()?.parameters));
    }
    /**
     * execute call with some conditions before send.
     * for example don't allow to fetch data until previus call has been received.
    */
    execute_method(method)
    {
        const app = this;
        if(this.is_loading === true){
            return;
        }
        method( app );
    }
}

export {FetchApp}