<script>
export let has_errors;
export let on_reload = function(){}
</script>
{#if has_errors}
    <div class="has-text-centered">
        <h2>
            Ha ocurrido un error inesperado.
        </h2>
        <br>
        <button class="button is-primary" on:click="{()=>{ on_reload() }}">
            Reintentar
        </button>
    </div>
{/if}