<script>
import { onMount } from "svelte";
import { clickOutside } from "../../libraries/clickOutside";


export let dropStyle = "";
export let listCont = "";
export let defaultList = true;


let status = "is-desactive"; 
let dropElement = null;
let dropContent = null;
function changeStatus(){
  if(dropElement === null){ return; }
  if(dropElement.classList.contains("is-active")){
    status = "is-desactive";
    dropElement.classList.remove("is-active");
  }else{
    status = "is-active";
    dropElement.classList.add("is-active");    
  }
}
$: if(dropContent !== null){
  clickOutside(dropElement, ()=>{
    status = "is-desactive";
    if(dropElement === null){ return; }
    dropElement.classList.remove("is-active");
  });
}

export let onClickContent = function(){};

</script>
<style>
  .items-drop{
    min-width: 30px;
    width: auto;
    height: auto;
    padding: 0;
  }
  .drop-background{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    height: 100%;
    min-height: 10rem;
    border-radius: 40px;
    margin-bottom: 1rem;
    padding: 0;
  }
  .items-drop.is-mid{
    width: 10rem;
    height: auto;
  }
  .items-drop.is-small{
    width: 2.7rem;
    height: auto;
  }
  .items-drop.is-big{
    width: 20rem;
    height: auto;
  }
  .drop-background.radius-none{
    border-radius: 0px;
  }
  .drop-background.low-radius{
    border-radius: 10px;
  }
  .nondefault{
    min-height: 24px;
    border-radius: 12px;
    margin-bottom: 0;
    width: fit-content;
    padding: 0.75rem 1.25rem;
  }
</style>
<div bind:this={dropElement} class="dropdown {dropStyle}">
  <div class="dropdown-trigger" on:click={changeStatus}>
    {#if $$slots.trigger}<!-- si no existe el slot trigger pasar el default. -->
        <slot name="trigger"></slot>
      {:else}
        <slot></slot>
    {/if}
  </div>
  {#if status === "is-active"}
    <div class="dropdown-menu items-drop {listCont}" on:click={()=>{ onClickContent(changeStatus) }} role="menu">
      {#if defaultList === true}
        <ul class="drop-background has-shadow {listCont}" bind:this={dropContent}>
          <slot name="content"></slot>
        </ul>
        {:else}
          <div class="drop-background has-shadow nondefault" bind:this={dropContent}>
            <slot name="content"></slot>
          </div>
      {/if}
    </div>
  {/if}
</div>
