<script>
import { ShadowRoot } from './../../ShadowRoot.js';
import FormModal from './layouts/FormModal.svelte';
import { onMount } from "svelte";
import ContainerModal from './layouts/ContainerModal.svelte';




export let id = "modal-"+turpial.random_string(6);

/**
 * pass has-responsive-height to set responsive
 */
export let classes = "";
export let classesContent = "";
export let classesHeader = "";
export let triggerClass = "";
export let styles = "";
export let has_responsive_tripod = "";
export let closeOnClickOverlay = true;
export let onClose = function(){};
export let onOpen = function(){};
export let active = false;
export let externStyles = "";
export let submit = ()=>{};
/**
 * versions: "default" or "form".
 * @type {String}
 */
export let type = "default"
export let layout = ContainerModal;

if(type === "form"){
    layout = FormModal;
}

let roleOverlay = "";

/**
 * - formElement
 * @type {HTMLElement}
 */
let modal = null;

/**
 * - container
 * @type {HTMLElement}
 */
let modal_section = null;


let OverlayClick = ()=>{}
if(closeOnClickOverlay === true){
    roleOverlay = "data-micromodal-close";
    OverlayClick = ()=>{
        active = false;
    }
}



function activeModal(){
    active = true;
}
function deactivateModal(){
    active = false;    
}
classes += " is-open"; 

$: {
    if(active === true && modal !== null){
    let container = modal.querySelector(".global-container-modal")
    /**
     * @type {HTMLElement}
     */
    let header = modal.querySelector("header");
    /**
     * @type {HTMLElement}
     */
    let footer = modal.querySelector("footer");
    /**
    * @type {HTMLElement}
    */
    let main = modal.querySelector("main");


    
    
    

    if(classes.search("has-responsive-height") >= 0){
        let height = 80;

        if(window.innerWidth <= 650){
        height = 95;
        }

        let heightCalc = `calc(${height}vh - ${header.offsetHeight+footer.offsetHeight}px)`;
        main.style.maxHeight = heightCalc;
    }

    
    $ShadowRoot.appendChild(modal);
    container.classList.add("animate__animated", "animate__fadeIn", "faster", "animated");
    onOpen(modal);
    }
}

function close(){
    modal_section.appendChild(modal);
    deactivateModal();
    onClose(modal);
}

</script>
<section class="modal-section {has_responsive_tripod === "has-responsive-tripod" ? "fit-content" : ""}" aria-hidden="true" bind:this={modal_section}>
    {#if active === true}
    <div class="custom-modal {classes} micromodal-slide" id="{id}" aria-hidden="true" bind:this={modal}>
        <svelte:component this={layout} submit={submit}>
            <section class="modal__overlay" tabindex="-1" data-trigger={roleOverlay} >
                <div class="overlay_trigger" on:click={OverlayClick}></div>
                <div style="background: white; border-radius: 20px; {externStyles}" class="global-container-modal {has_responsive_tripod}">
                <button class="modal__close" aria-label="Close modal" data-micromodal-close on:click="{()=>{ close() }}" type="button">
                    <i class="icon-tripod-times has-text-lighter"></i>
                </button>
                <header class="modal__header {classesHeader}">
                    <slot name="title" {active} hide={deactivateModal} open={activeModal}></slot>
                    <slot name="header" {active} hide={deactivateModal} open={activeModal}></slot>                  
                </header>
                <main style="{styles}" class="modal__container custom-scrollbar-gray {classesContent}" role="dialog" aria-modal="true" aria-labelledby="{id}-title">
                    <div class="modal__content {classesContent}" id="{id}-content">
                        <slot name="content" modal={modal} {active} hide={deactivateModal} open={activeModal}></slot>
                        <slot {active} modal={modal} hide={deactivateModal} open={activeModal}></slot>
                    </div>
                </main>
                <footer class="modal__footer" id="{id}-footer">
                    <slot name="footer" {active} hide={deactivateModal} open={activeModal}></slot>
                </footer>
                </div>
            </section>
        </svelte:component>
    </div>
    {/if}
<a class="{triggerClass}" style="text-decoration: none;" href="# " on:click|preventDefault={activeModal}>
    <slot name="trigger"></slot>
</a>
</section>


<style>
    .modal__container{
        overflow-y:auto;padding-bottom:60px;
    }
    /**************************\
    Basic Modal Styles
    \**************************/
    
    .modal__overlay {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgb(80 78 96 / 45%);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 50;
    }
    
    
    .modal__container.padding-0 {
        padding: 0;
    }
    
    .modal__header {
        display: flex;
        justify-content: end;
        align-items: center;
    }
    
    .modal__header.is-absolute-right {
        position: absolute !important;
        right: 1rem;
        top: 1rem;
    }
    
    .modal__title {
        margin-top: 0;
        margin-bottom: 0;
        font-weight: 600;
        font-size: 1.25rem;
        line-height: 1.25;
        color: #00449e;
        box-sizing: border-box;
    }
    
    .modal__close {
        background: transparent;
        border: 0;
        position: absolute;
        top: 40px;
        right: 40px;
        z-index: 12;
    }
    
    .modal__header .modal__close:before { content: ""; }
    
    .modal__content {
        margin-top: 2rem;
        margin-bottom: 2rem;
        line-height: 1.5;
        color: rgba(0,0,0,.8);
    }
    .modal__content.margin-0{
        margin-top: 0;
        margin-bottom: 0;
    }
    
    .modal__btn {
        font-size: .875rem;
        padding-left: 1rem;
        padding-right: 1rem;
        padding-top: .5rem;
        padding-bottom: .5rem;
        background-color: #e6e6e6;
        color: rgba(0,0,0,.8);
        border-radius: .25rem;
        border-style: none;
        border-width: 0;
        cursor: pointer;
        -webkit-appearance: button;
        text-transform: none;
        overflow: visible;
        line-height: 1.15;
        margin: 0;
        will-change: transform;
        -moz-osx-font-smoothing: grayscale;
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
        transition: -webkit-transform .25s ease-out;
        transition: transform .25s ease-out;
        transition: transform .25s ease-out,-webkit-transform .25s ease-out;
    }
    
    .modal__btn:focus, .modal__btn:hover {
        -webkit-transform: scale(1.05);
        transform: scale(1.05);
    }
    
    .modal__btn-primary {
        background-color: #00449e;
        color: #fff;
    }
    
    
    
    /**************************\
    Demo Animation Style
    \**************************/
    .micromodal-slide {
        display: none;
    }
    
        .micromodal-slide.is-open {
        display: block;
    }
    
    .micromodal-slide .modal__container,
    .micromodal-slide .modal__overlay {
        will-change: transform;
    }
    
    .modal__container{
        background-color: #fff;
        padding: 20px;
        min-width: 320px;
        width: 100%;
        max-height: 100vh;
        min-height: 50px;
        border-radius: 20px;
        position: relative;
        z-index: 10;
        box-sizing: border-box;
        overflow-y: auto;
        animation: fadeIn; /* referring directly to the animation's @keyframe declaration */
        animation-duration: 0.125s; /* don't forget to set a duration! */
    }
    .custom-modal{
        position: relative;
        z-index: 1000;
    }
    .custom-modal [data-micromodal-close]{
        cursor: pointer;
    }
    a{
        width: auto;
        height: 100%;
    }
    .overlay_trigger{
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 5;
    }
    .full-height{
        height: 100%;
    }
    footer{
        position: sticky;
        bottom: 0;
        background-color: #fff;
    }
    header{
        position: sticky;
        bottom: 0;
        background-color: #fff;
    }
    .global-container-modal{
        position: relative;
        z-index: 20;
        border-radius: 20px;
        padding: 40px;
    }
    .is-responsive :global(.modal__container){
        max-height: 60vh;
    }
    /* responsive tripod design */
    .global-container-modal.has-responsive-tripod{
        max-width: 928px;
        box-sizing: border-box !important;
    }
    .global-container-modal.has-responsive-tripod main{
        height: 100% !important;
        max-height: calc(82.5vh) !important;
        padding: 20px 0 0 0 !important;
        border-radius: 0 !important;
    }
    .global-container-modal.has-responsive-tripod main .modal__content{
        height: 100% !important;
        margin-top: 0 !important;
        margin-bottom: 0 !important;
    }

@media only screen and (max-width:1280px){
    .global-container-modal.has-responsive-tripod{
        width: 600px;
    }
}

@media only screen and (max-width:900px){
    .global-container-modal.has-responsive-tripod{
        width: 500px;
    }
    .global-container-modal.has-responsive-tripod .modal__close{
        top: 20px !important;
        right: 20px !important;
    }
}
@media only screen and (max-width:800px){
    .global-container-modal.has-responsive-tripod{
        width: 100vw;
        height: 100vh;
        border-radius: 0 !important;
    }
    .global-container-modal.has-responsive-tripod main{
        max-height: calc(100vh - 80px) !important;
    }
}
@media only screen and (max-width:500px){
    .global-container-modal.has-responsive-tripod{
        padding: 20px !important;
    }
    .global-container-modal.has-responsive-tripod main{
        max-height: calc(100vh - 40px) !important;
    }
}
</style>