/*!
 * smallPop 0.1.2 | https://github.com/silvio-r/spop
 * Copyright (c) 2015 Sílvio Rosa @silvior_
 * MIT license
 */

var root_target = document.body;

(function() {
	var animationTime = 390;
	var options, defaults, container, icon, layout, popStyle, positions, close;
	
	
	var SmallPop = function(template, style, target) {		
		root_target = target || document.body;

		this.defaults = {
			template  : null,
			style     : 'info',
			autoclose : false,
			position  : 'top-right',
			icon      : true,
			group     : false,
			onOpen    : false,
			onClose   : false
		};

		defaults = extend(this.defaults, spop.defaults);

		if ( typeof template === 'string' || typeof style === 'string' ) {
			options = { template: template, style: style || defaults.style};
		}
		else if (typeof template === 'object') {
			options = template;
		}
		else {
			console.error('Invalid arguments.');
			return false;
		}

		this.opt = extend( defaults, options);

		if ($('spop--' + this.opt.group)) {

			this.remove($('spop--' + this.opt.group));
		}

		this.open();
	};

	SmallPop.prototype.create = function(template) {

		container = $(this.getPosition('spop--', this.opt.position));
        function createIcon(icon){
            return `<i class="${icon}"></i>`
        }
        let icon_element = "";
        switch (this.opt.style) {
            case "success":
                icon_element = createIcon("has-text-success icon-tripod-checklist-alt font-size-40")
            break;
            case "info":
                icon_element = createIcon("has-text-primary icon-tripod-info-alt font-size-40")
            break;
            case "warning":
                icon_element = createIcon("has-text-warning icon-tripod-warning-alt font-size-40")
            break;
            case "error":
                icon_element = createIcon("has-text-danger icon-tripod-alert-outlined font-size-40")
            break;
            default:
                icon_element = (!this.opt.icon) ? '' : createIcon("has-text-primary icon-tripod-info-alt font-size-40");
        }

        icon = icon_element;
		
        layout = `<div class="spop-close" data-spop="close" aria-label="Close">
			<i class="icon-tripod-times has-text-lighter font-size-16"></i>
		</div>
            <div class="spop-body">
                <span>
                    ${icon}
                </span>
                ${template}
            </div>
        `
		if (!container) {

			this.popContainer = document.createElement('div');

			this.popContainer.setAttribute('class', 'spop-container ' +
				this.getPosition('spop--', this.opt.position));

			this.popContainer.setAttribute('id', this.getPosition('spop--', this.opt.position));

			root_target.appendChild(this.popContainer);

			container = $(this.getPosition('spop--', this.opt.position));
		}

		this.pop = document.createElement('div');

		this.pop.setAttribute('class', 'spop spop--out spop--in ' + this.getStyle('spop--', this.opt.style) );

		if (this.opt.group && typeof this.opt.group === 'string') {
			this.pop.setAttribute('id', 'spop--' + this.opt.group);
		}


		this.pop.setAttribute('role', 'alert');

		this.pop.innerHTML = layout;

		container.appendChild(this.pop);
	};

	SmallPop.prototype.getStyle = function(sufix, arg) {

		popStyle = {
			'success': 'success',
			'error'  : 'error',
			'warning': 'warning'
		};
		return sufix + (popStyle[arg] || 'info');
	};

	SmallPop.prototype.getPosition = function(sufix, position) {

		positions = {
			'top-left'     : 'top-left',
			'top-center'   : 'top-center',
			'top-right'    : 'top-right',
			'bottom-left'  : 'bottom-left',
			'bottom-center': 'bottom-center',
			'bottom-right' : 'bottom-right'
		};
		return sufix + (positions[position] || 'top-right');
	};

	SmallPop.prototype.open = function() {

		this.create(this.opt.template);

		if (this.opt.onOpen) { this.opt.onOpen();}

		this.close();
	};

	SmallPop.prototype.close = function () {

		if (this.opt.autoclose && typeof this.opt.autoclose === 'number') {

			this.autocloseTimer = setTimeout( this.remove.bind(this, this.pop), this.opt.autoclose);
		}

		this.pop.addEventListener('click', this.addListeners.bind(this) , false);
	};

	SmallPop.prototype.addListeners = function(event) {

		close = event.target.getAttribute('data-spop');

		if (close === 'close') {

			if (this.autocloseTimer) { clearTimeout(this.autocloseTimer);}

			this.remove(this.pop);
		}
	};

	SmallPop.prototype.remove = function(elm) {

		if (this.opt.onClose) { this.opt.onClose();}

		removeClass(elm, 'spop--in');

		setTimeout( function () {
			if(root_target.contains(elm)) {
				elm.parentNode.removeChild(elm);
			}

		}, animationTime);
	};


	// Helpers

	function $(el, con) {
		return typeof el === 'string'? (con || root_target).querySelector(`#${el}`) : el || null;
	}

	function removeClass(el, className) {
		if (el.classList) {
			el.classList.remove(className);
		}
		else {
			el.className = el.className.replace(new RegExp('(^|\\b)' +
							className.split(' ').join('|') +
							'(\\b|$)', 'gi'), ' ');
		}
	}

	function extend(obj, src) {

		for (var key in src) {
			if (src.hasOwnProperty(key)) obj[key] = src[key];
		}

		return obj;
	}

	window.spop = function (template, style) {
		if ( !template || !window.addEventListener ) { return false;}
		template.root_target = template.root_target || document.body;
		return new SmallPop(template, style, template.root_target);
	};

	spop.defaults = {};
}());

/**
 * @param html {String} HTML o String
 * @param type {String} success, info, warning, error.
 * @param close {Int} Cuantos segundos tardará en desaparecer
 * @param position {String} {String} Default: "top-right": 
 * "Top left", "Top center", "Top right", "Bottom left", "Bottom center", "Bottom right"
 * 
*/
const execute_toast = function({html, type, close, position, styles = "max-width:250px;word-break: break-word;"}){
	html = html||"";type = type||"success";close = close||3500;position = position||"bottom-right";

	if(typeof window.document_root !== "undefined"){
		root_target = window.document_root;
	}

    return spop({ template: `<div style="${styles}">${html}</div>`, style: type, position: position, autoclose: close, root_target: root_target});
}

const toast = function(html, type, close,position) {
    return execute_toast({
		html: html,
		type: type,
		close: close,
		position: position,
		styles: "max-width:250px;word-break: break-word;"
	})
}

const toast_alt = function(html, type, close,position) {
	return execute_toast({
		html: html,
		type: type,
		close: close,
		position: position,
		styles: "word-break: break-word;"
	})
}

/**
 * para notificaciones con título y layout según mockup.
 *
 * @param title {String} Título.
 * @param content {String} Contenido.
 * @param type {Int} success, info, warning, error.
 * @param close {String} Cuantos segundos tardará en desaparecer. 
 * @param position {String} {String} Default: "top-right": 
 * "Top left", "Top center", "Top right", "Bottom left", "Bottom center", "Bottom right"
 * 
 */
const toast_pop = function(title, content, type, close, position) {
    title = title || "";
    content = content || "";
    type = type || "success";
    let title_color = "has-text-primary";
    close = close||3500;
    position = position||"bottom-right";

    switch (type) {
        case "success":
            title_color = "has-text-success"
        break;
        case "warning":
            title_color = "has-text-warning"
        break;
        case "error":
            title_color = "has-text-danger"
        break;
    
        default:
            title_color = "has-text-primary"
        break;
    }

    title = `<h6 class="${title_color} font-size-16">${title}</h6>`
    content = `<p>${content}</p>`

    let html = `<div>${title+content}</div>`;

    
    spop({ template: `<div style="max-width:320px;word-break: break-word;">${html}</div>`, style: type, position: position, autoclose: close });
}

/**
 * para notificaciones con título y layout según mockup.
 *
 * @param message {String} Título.
 * @param onConfirm {Function} Al dar clic en confirmar
 * @param onCancel {Function} Al dar clic en cancelar
 * @param cancelButton {String} 
 * @param confirmButton {String}
 * 
 */
const confirmToast = function (message, {onCancel, onConfirm, cancelButton, confirmButton, cancel_button_class, confirm_button_class, type}){
    let id = turpial.random_string(16);
    
    confirmButton = confirmButton||"Sí";
    onConfirm = onConfirm||function(){};
    onCancel = onCancel||function(){};
    cancelButton = cancelButton||"No";
    cancel_button_class = cancel_button_class || "";
    confirm_button_class = confirm_button_class || "";
	type = type || "info";
    
    let html = `
        <div class="is-flex-desktop is-align-items-center gap-18 is-block-touch">
            <h6 class="font-size-14 fw400" style="max-width:320px;">
                ${message}
            </h6>
            <div class="buttons are-small field is-flex is-flex-direction-column is-align-items-centered is-grouped" id="${id}" style="flex-direction: row !important;">
                <div class="control ${confirm_button_class}">
                    <button type="button" data-spop="close" class="button is-primary confirmButton">
                        ${confirmButton}
                    </button>
                </div>
				<div class="control ${cancel_button_class}">
                    <button type="button" data-spop="close" class="button is-primary is-outlined cancelButton">
                        ${cancelButton}
                    </button>
                </div>
            </div>
        </div>`;


	toast_alt(html, type, true, "bottom-right");

	let supercontainer = window.document_root ?? root_target ?? document.body;

    let container = root_target.querySelector(`#${id}`);
    confirmButton = container.querySelector(".confirmButton");
    cancelButton = container.querySelector(".cancelButton");

    confirmButton.addEventListener("click", ()=>{
        onConfirm();
    })
    cancelButton.addEventListener("click", ()=>{
        onCancel();
    })
}

export {toast, toast_pop, confirmToast};