<script> 
import SidebarElement from './../components/Menus/Sidebar.svelte';
import DefaultHeader from './partials/DefaultHeader.svelte';
import DefaultFooter from 'tripod-styles/src/components/Sections/Footer.svelte';

/**
 * global props
 */
export let props = {};


/**
 * components
 */
export let Header = props?.Header ?? DefaultHeader;
export let Footer = props?.Footer ?? DefaultFooter;
export let Sidebar = props?.Sidebar ?? SidebarElement;

/**
 * footer, header and sidebar
 */
export let has_header = props?.has_header ?? true;
export let has_footer = props?.has_footer ?? true;
export let has_sidebar = props?.has_sidebar ?? false;


/**
 * classes and top spacing
 */
export let has_top_spacing = props?.has_top_spacing ?? true;
export let classes = props?.classes ?? "container";
export let articleStyles = props.articleStyles ?? `
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100% !important;
`;
export let classes_main_container = props?.classes_main_container ?? "";
let sidebar_class = has_sidebar === false ? "" : "has_sidebar";


has_top_spacing = has_top_spacing === true ? "has_top_spacing" : "";

</script>
<style> 
.container{    
    max-width: 1440px;
    margin: auto;
}
.container :global(.has_top_spacing){
    margin-top: 1.25rem;
    padding: 0 2rem 0 2rem;
}
article:global(.has_sidebar){
    width: calc(100% - 3.625rem);
    padding-left: 3.625rem;
}
@media only screen and (max-width: 1023px){
    article:global(.has_sidebar){
        width: 100%;
    }
}
@media only screen and (max-width: 400px){
    .container :global(.has_top_spacing){
        padding: 0 1rem 0 1rem;
    }
}
</style>
{#if has_header}
    <svelte:component this={Header} />
{/if}
{#if has_sidebar}
    <svelte:component this={Sidebar} />
{/if}
<article class="{classes} {sidebar_class}" style="{articleStyles}">
    <section class="public-container {has_top_spacing} {classes_main_container}">
        <slot></slot>
    </section>
    {#if has_footer}
        <footer class="full-width mb-alt-8">
            <svelte:component this={Footer} />
        </footer>
    {/if}
</article>