import DefaultTemplate from "../layouts/DefaultTemplate.svelte";
import { page } from "../turpial.route.store";

/**
 * 
 * @param {ClassInstance} AppRoute Contains methods for redirections and setters for sub routes
 * @var {Object} AppRoute.path contains current parameters and url path. 
 * @var {Object} AppRoute.parameters function to create a return result with all url paramters.
 * @var {function} page.route use this method to create a redirection. example: page.route("user/dashboard")
 * @returns {Object}
 */
const IndexRoute = ()=>{
    class IndexApplication
    {
        constructor(){
            
            return this.index();
        }
        index(){
            this.props = {
                has_top_spacing: false,
                has_footer: true,
                classes: "flex-column-space-between gap-13 full-size",
                classes_main_container: "is-flex is-align-items-center is-justify-content-center full-width",
                articleStyles: "margin:auto; box-sizing: border-box; padding-top: 3rem;"
            };

            this.template = DefaultTemplate;

            this._render = ()=> import("../views/private/MyChannel.svelte").then((r)=> r)
            
            
            return this;
        }
    }
    
    return new IndexApplication();
};

export default IndexRoute;