
<script>
	import RouterRender from './RouterRender.svelte';
	import RouterModule from './RouterModule.svelte';
	import _404 from '../views/errors/_404.svelte';
import AppDefaultLayout from './AppDefaultLayout.svelte';
import { RouterStore } from './../RouterStore.js';
import DefaultLayout from './../layouts/DefaultTemplate.svelte';
import RouterConditions from './../router.conditions.js';
import { page } from '../turpial.route.store.js';
import Routes from '../routes.js';
import Loader from '../components/Loader.svelte';
import { turpial } from '../libraries/turpial';
import { RouterCases } from './RouterCases';
    import { RouterModel } from '../models/Routes.model';


export let on_controller = "";
export let on_action = "";
export let on_parameters = "";
export let CustomRoute = false;

const reserved_words = /_render|_limit_params|template|props|params|render_on_toggle/;

export let path = "";
let allFails = true;
let routes = path.split("/");
let controller_name = "";
let action_name = "";
let current_view = null;
let current_controller_view = "";
let render_on_toggle = true;
let parameters = [];
routes.map(( selector, key )=>{
	if(key === 0){
		controller_name = selector;
		return;
	}
	if(key === 1){
		action_name = selector;
		return;
	}
	parameters.push(selector);
})
if(controller_name === ""){
	controller_name = "index";
}
let views = {
	controller_name: controller_name,
	action_name: action_name,
	parameters: parameters,
}

views = `${views.controller_name}/${views.action_name}/${views.parameters.join("/")}`;
views = views.split("///").join("/");
views = views.split("//").join("/");


let RoutePath = `${CustomRoute.controller_name}/${CustomRoute.action_name}/${CustomRoute.parameters.join("/")}`;
RoutePath = RoutePath.split("///").join("/");
RoutePath = RoutePath.split("//").join("/");

$: {
	if(CustomRoute){
		RoutePath = `${CustomRoute.controller_name}/${CustomRoute.action_name}/${CustomRoute.parameters.join("/")}`;
	}
}

if(RoutePath.slice(-1) === "/"){
	RoutePath = RoutePath.slice(0, RoutePath.length - 1)
}

if(path !== ""){
	on_controller = controller_name;
	on_action = action_name;
	on_parameters = parameters;
}

let layout = AppDefaultLayout;
let props = {};
let params = [];
let app_routes = null;

function catch_error(return_layout){
	if(return_layout){
		layout = DefaultLayout;
		return layout;
	}
	return Routes.errors["404"]._render();
}



let rendering_position = 0;
function showView(structure, return_layout = false){
	structure.route_path = RoutePath;
	structure.view = RoutePath;
	if(structure.path.search(reserved_words) >= 0){
		console.error("Reserved Words Found.")
		/**
		 * para layouts
		*/
		if(return_layout === true){ return DefaultLayout }


		/**
		 * para vistas
		*/
		return catch_error(return_layout);
	}
	
	RouterConditions(structure.controller_name, structure.action_name, structure.path);

	/**
	 * @type {RouterModel}
	 */
	let view = null;
	let Constructor = null;
	let has_constructor = false;
	
	if(typeof Routes[structure.controller_name] === "object"){

		/**
		 * en caso de pasarle un constructor o clase
		*/
		try {
			if(typeof Routes[structure.controller_name].constructor.name !== "undefined"){
				Constructor = Routes[structure.controller_name];
				has_constructor = true;
			}
		} catch (error) {

		}
		
		
		
		let action_roter = Routes[structure.controller_name][structure.action_name];
		let constructor_parameters = [];
		let init_constructor = function(){
			app_routes = new RouterCases(structure, return_layout)
			view = Constructor[structure.action_name](app_routes);
			
			try {
				view.params = view?.params ?? {};
				constructor_parameters = view.params.split("/");
			} catch (error) {

			}
			
		}
		
		if(typeof action_roter === "object" || typeof action_roter === "function"){
	
			view = action_roter;
			if(typeof view === "function"){
				init_constructor();
			}

			


			let action = view;
			let limit_params = action?._limit_params ?? true;
			
			
			if(structure.parameters.length > 0 ||  view?.constructor.name !== "Object"){
				var errorParams = false;				
				structure.parameters.map(( param, key )=>{
					try {
						if(typeof view[param] !== "undefined" && typeof view[param] !== "function"){
							view = view[param];						
						}else{
							errorParams = true;
						}
					} catch (error) {
						console.error(error);
						return catch_error(return_layout);
					}
				})

				
				if(limit_params === true && errorParams === true){

					/**
					 * para layouts
					*/
					if(return_layout === true){ return DefaultLayout }


					/**
					 * para vistas
					*/
					return catch_error(return_layout);
				}
			}
			

		}else{
			if(structure.action_name === ""){
				view = Routes[structure.controller_name];
				if(typeof view.index === "function"){
					view = view.index(props);
				}
			}
		}
	}
	
	
	props = view?.props ?? {};
	
	
	let has_parameter = false;
	let in_position = false;
	let has_invalid_parameter = false;
	let view_params = view?.params ?? {};
	
	if(typeof view_params !== "undefined" && structure.parameters.length !== 0 && Constructor == null){	
		Object.keys(view_params).map(( routes, key )=>{
			
			has_parameter = true;
			
			let route_splitted = routes.split("/");
			
			if(route_splitted.length === structure.parameters.length){
				props.parameters = [];
				route_splitted.map(function( item, key ){
					
					let is_parameter = item.search("{") >= 0 ? true : false;
					let route = item.split("{").join("").split("}").join("");
					
					if(is_parameter === false){
						if(structure.parameters[key] !== item){
							props.parameters = [];
							has_invalid_parameter = true;
							return;
						}
					}

					in_position = true;
					view = Object.values(view_params)[structure.parameters.length-1];
					
					
					
					props.parameters[route] = structure.parameters[key];

				})	
					
			}
			

			
			
			
		})
	}else{
		props.parameters = [];
	}
	
	

	//props.props = view?.props ?? {};
	RouterStore.update(props);
	if (Constructor !== null) {
		if(app_routes == null){
			app_routes = new RouterCases(structure, return_layout)
		}
		app_routes.set_props(return_layout);
	}
	
	if(typeof (view?.template ?? null) !== null){
		layout = view?.template ?? AppDefaultLayout;
	}

	if(has_parameter === true && in_position === false && has_invalid_parameter === true){
		view = null;
	}
	
	if(view === null || typeof view === "undefined"){
		return catch_error(return_layout);
	}

	if(typeof view._render !== "function"){
		console.error("Ha habido un error inesperado al procesar vistas.");
		return catch_error(return_layout);
	}

	/**
	 * calcular la posición del rendering
	 * siempre se ejecuta dos veces la primera vez (al ingresar desde el navegador)
	 * luego el template no se vuelve a ejecutar a menos que cambie.
	*/
	/*rendering_position++;
	let is_template = false;
	if(rendering_position === 2){
		is_template = true;
		rendering_position = 0;
	}*/
	
	/*if(is_template === false){
		if(typeof view.render_on_toggle !== "undefined"){
			if(view.render_on_toggle === false){
				if(view._controller === current_controller_view){
					return "no-render-toggle"
				}
			}
		}
		console.log(view._controller === current_controller_view)
	}*/
	

	current_view = view._valid_case;
	current_controller_view = view._controller;
	render_on_toggle = view.render_on_toggle;

	return view._render();
}

</script>
{#if CustomRoute !== false}
	{#await showView(CustomRoute)}
			<Loader status="show" />
		{:then module}
		{#if typeof module !== "undefined" && module !== false && module !== null}
			{#if module !== "no-render-toggle"}
					<RouterModule 
					render_on_toggle={render_on_toggle}
					view={current_view}
					controller={current_controller_view}
					component={module.default} props={props} />

				<Loader status="hide" />
			{/if}
		{/if}
	{/await}
	{:else}
	{#await showView($page)}
			<Loader status="show" />
		{:then module}			
		{#if typeof module !== "undefined" && module !== false && module !== null}
			{#if module !== "no-render-toggle"}
					<RouterModule 
					render_on_toggle={render_on_toggle}
					view={current_view}
					controller={current_controller_view}
					component={module.default} props={props} />

				<Loader status="hide" />
			{/if}
		{/if}
	{/await}
{/if}
<svelte:component props={props} this="{layout}" >
	<RouterRender />
</svelte:component>