import { writable } from 'svelte/store';

function routerStore() {


	const { subscribe, update } = writable({
        parameters: {},
        props: {},
        action_name: null,
        controller_name: null,
    });
	
	const routes = {
        subscribe: subscribe,
        set: (field, new_data)=>{
            update(( data )=>{
                data[field] = new_data;
                return data;
            })
        },
        update: (new_data)=>{
            update(( data )=>{
                data = new_data;
                return data;
            })

            return new_data;
        },
        get: function(){
            let returns = {};
            update(function( data ){
                returns = data;
                return data;
            })
            return returns;
        }
    }
    return routes;
}


export const RouterStore = routerStore();