import { server_app3pod, server_connection } from '../../../api/PHPserver.js'
import { ChannelData } from '../ChannelData.store';
import axios from 'axios';

class AuthenticationChannel 
{
    authentication(token1, token2, params, callback){
        params = params ?? "";
        let url = server_app3pod(`user/auth/${token1}/${token2}/stream${params}`)
        axios.get(url).then((response)=>{
            let jwt_token = response.data.token;
            let plan_limits = response.data.plan_limits;

            ChannelData.update('channel_token', jwt_token);
            ChannelData.update('plan_limits', plan_limits);
            this.createChannel(callback);
        })
    }
    createChannel(callback){
        let url_post = server_connection('stream/create_channel/main');
        axios.post(url_post, {}, {
            headers: {
                "X-Token": ChannelData.get().channel_token
            }
        }).then((data) => {
            ChannelData.update('channel_data', data.data);
            // getVideosFromChannel();
            callback(data.data);
        })
    }
}

export { AuthenticationChannel };