<script>
import { RouterModuleStore } from "./RouterCases";


export let component;
export let props = {};
export let controller = "";
export let view = "";
export let render_on_toggle = true;



RouterModuleStore.set_module(view, component)
RouterModuleStore.set_controller(controller)
RouterModuleStore.set_props(props);
RouterModuleStore.set_property("render_on_toggle", render_on_toggle)

</script>