<section class="flex-column full-size margin-container section-design" style="box-sizing: border-box;" bind:this={container}>
    <header class="flex-column-flex-start full-width gap-alt-8">
        <h1 class="font-size-28 fw700">Administrador de videos</h1>
        <p class="has-text-grey fw500">Almacena tus videos de forma sencilla.</p>
    </header>
    <article class="columns is-multiline full-size flex-center">
        <div class="column" style="min-width: 295px;">
            <figure class="flex-center">
                <img src="https://i.postimg.cc/h4HkdNcf/image-53.png" alt="tripod-video-admin-logo">
            </figure>
        </div>
        <div class="column" style="min-width: 295px;">
            <div class="info-container flex-column-flex-start gap-13">
                <div>
                    <h5 class="has-text-primary fw600">¿En que consiste el administrador de videos?</h5>
                </div>
                <div class="flex-column-flex-start gap-alt-8">
                    <h2 class="fw700">Adiós Vimeo, hola 3Pod!</h2>
                    <p class="has-text-grey fw500">Sube y administra tus videos, podrás usarlos dentro de nuestra plataforma de 3Pod con solo copiar y pegar el enlace en el constructor.</p>
                </div>
                <div style="height: 300px; width: 100%;">
                    <iframe 
                        width="100%" 
                        height="100%" 
                        src="https://www.youtube.com/embed/jrYohejTcv4" 
                        title="YouTube video player" 
                        frameborder="0" 
                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
                        allowfullscreen>
                    </iframe>
                </div>
                <div class="full-width" style="max-width: 191px;">
                    <a href="{$ApplicationData.go_to_plans}" class="button is-fullwidth is-primary has-not-animated" style="margin-top: 0px !important; margin-bottom: 0px !important;">Adquirir membresía</a>        
                </div>
            </div>
        </div>
    </article>
</section>

