import { server_app3pod, server_connection } from '../../../api/PHPserver.js'
import { ChannelData } from '../ChannelData.store';
import axios from 'axios';
import { ThumbnailController } from './ThumbnailController.js';
import { confirmToast, toast } from '../../../libraries/toast.js';

class VideoController 
{
    constructor(key, paginator)
    {
        this.uid = "";
        this.key = key || "";
        this.image_url = "";
        this.thumbnails_group = [];
        this.thumbnails_bolbs = [];
        this.paginator = paginator;
        this.errorDetails = {};
        this.counter = 0;
    }
    prepareUploadVideo(files_info){    
        /**
         * Preparamos la subida del vídeo con una URL firmada y de uso único.
         */
        let url = server_connection(`/stream/prepare_upload/${ChannelData.get().channel_data.id}`);
        axios.post(url, {
            "duration": Math.ceil(files_info.video_duration),
            "title": files_info.title
        }, {
            headers: {
                "X-Token": ChannelData.get().channel_token
            }
        }).then((data)=>{
            let result = data.data;
            let uploadURL = data.data.video_upload.result.uploadURL;
            this.uid = data.data.uid;
            this.image_url = files_info.thumbnail;
            this.thumbnails_group = files_info.thumbnails_group;
            this.thumbnails_blobs = files_info.thumbnails_blobs;
            this.errorDetails = files_info.errorDetails;

            result.percentage = files_info.file.percentage;
            result.is_thumbnail_ready = files_info.is_thumbnail_ready;
            result.thumbnails_group = files_info.thumbnails_group;
            result.update_title = false;
            result.update_thumbnails = false;

            ChannelData.updateProcess(result, "is_loading", 5, this.key);
            /**
             * con el upload url subimos el archivo.
             * después de esto se esperar por la respuesta del servidor
             */
             ChannelData.get().proccesing_files.map((item, key)=>{
                if (item.stackId === this.key) {
                    this.uploadVideo(uploadURL, files_info);
                }
             })
        }).catch((err) => {
            this.counter = this.counter + 1;

            if(this.counter <= 4){
                this.prepareUploadVideo(files_info);
            }
            else{
                this.counter = 0;
                let details = {
                    msg: "no se pudo preparar el espacio",
                    type: "prepareUploadVideo",
                    reload: () =>{
                        this.prepareUploadVideo(files_info);
                    }
                }
                // console.log(err);
                ChannelData.errorController(files_info.file.stackId, details, true);
            }
        })
    }
    /**
     * Subir video a cloudflare stream.
     * @param {String} uploadURL 
     * @param {object} files 
     */
    uploadVideo(uploadURL, files){
        const form = new FormData();
        form.append('file', files.file);
        
        const response = axios.post(uploadURL, form).then((response)=>{
            ChannelData.get().proccesing_files.map((item, key)=>{
                if (item.stackId === this.key) {
                    item.update_title = true;
                    ChannelData.updateProcess(item, "is_loading", 30, this.key);
                }
             })


            let thumbnail = new ThumbnailController(this.key, this.paginator);
            thumbnail.parseThumbnails(this.uid, this.thumbnails_blobs);


            /**
             * start a real-time connection.
             */
            try {
                let uid = this.uid; // id del vídeo.
                let channel = `public:stream_videos_status:video=eq.${uid}`;
                let supabase = window.$supabase;

                supabase 
                .channel( channel )
                .on('postgres_changes', {
                    event: 'UPDATE',
                    schema: 'public',
                    table: 'stream_videos_status', 
                    filter: `video=eq.${uid}` 
                }, ( payload)=>{
                    if(payload.new.internal_status === "ready"){
                        ChannelData.get().proccesing_files.map((item, key)=>{
                            if (item.stackId === this.key) {
                                this.getSingleVideo(item.uid, (data)=>{
                                    let result = data;
                                    result.stackId = item.stackId;
                                    result.percentage = item.percentage;
                                    result.is_thumbnail_ready = true;
                                    ChannelData.updateProcess(result, "is_loading", 30, this.key);
                                })
                            }
                        })
                        ChannelData.deleteStackItem(this.key);
                        if (ChannelData.get().stack.length <= 0) {
                            this.paginator.get().submit();
                        }
                        supabase.removeChannel( channel )
                    }
                }).subscribe()

            } catch (error) {
                let details = {
                    msg: "Hubo un problema al procesar el video",
                    type: "uploadVideo Supabase Event",
                    reload: () =>{
                        let thumbnail = new ThumbnailController(this.key);
                        thumbnail.parseThumbnails(this.uid, this.thumbnails_blobs);
                    }
                }
                ChannelData.errorController(this.uid, details);
            }
        }).catch((error) => {
            // console.error(error.response.data "File size too large status=413");
            //uploadVideo to cloudfare
            this.counter = this.counter + 1;
            if(this.counter <= 4){
                this.uploadVideo(uploadURL, files);
            }
            else{
                this.counter = 0;
                let details = {
                    msg: "Hubo un problema al procesar el video",
                    type: "uploadVideo to cloudfare",
                    reload: () =>{
                        let thumbnail = new ThumbnailController(this.key);
                        thumbnail.parseThumbnails(this.uid, this.thumbnails_blobs);
                    }
                }
                ChannelData.errorController(this.uid, details);
            }
        });
        
    }
    // getVideosFromChannel(channel, callback = ()=>{}){
    //     let url = server_connection(`stream/channel/${channel.id}/videos`);
    //     axios.get(url, {
    //         headers: {
    //             "X-Token": ChannelData.get().channel_token
    //         }
    //     }).then((data)=>{
    //         ChannelData.update('files', data.data.data.reverse());
    //         callback(data);
    //     })
    // }
    updateVideo(video_uid, new_data, callback, is_upload = false){
        if (is_upload) {
            if(new_data.title){
                if(new_data.title.length > 600){
                    toast("Solo puedes subir vídeos con un titulo de maximo 600 caracteres", "warning", 3000, "bottom-right");
                    return;
                }
            }else if (new_data.thumbnail.split('.').at(-1)) {
                if (new_data.thumbnail.split('.').at(-1) !== "jpg" && new_data.thumbnail.split('.').at(-1) !== "jpeg") {
                    toast("Solo puedes subir imagenes con un formato jpg o jpeg", "warning", 3000, "bottom-right");
                    return;
                }
            }else{
                //console.log(new_data);
            }
        }
        let url = server_connection(`stream/video/${video_uid}`);

        // se puede pasar solamente title o solamente thumbnail.

        axios.put(url, new_data, {
            headers: {
                "X-Token": ChannelData.get().channel_token
            }
        }).then((data)=>{
            callback(data);
        }).catch((error)=>{
            
            //validar cambios de miniaturas y tutilo del video
        })
    }
    getSingleVideo(uid, callback){
        let url = server_connection(`stream/video/${uid}`);
        axios.get(url, {
            headers: {
                "X-Token": ChannelData.get().channel_token
            }
        }).then((data)=>{
            callback(data.data);
        }).catch((error) => {
            // console.log(error);
        })
    }
    downloadVideo(uid){
        let url = `https://stream.3pod.io/api/v1/services/stream/video/${uid}/download`
        axios.get(url, {
            headers: {
                "X-Token": ChannelData.get().channel_token
            }
        }).then((url)=>{
            window.location.href=url.data;
        })
    }
    deleteVideo(uid, callback){
        confirmToast("¿Seguro que desea eliminar este vídeo?", {
            onConfirm: ()=>{
                let url = `https://stream.3pod.io/api/v1/services/stream/videos`;
                fetch(url, {
                    method: "DELETE",
                    headers: {
                        "X-Token": ChannelData.get().channel_token
                    },
                    body: JSON.stringify({
                        videos: [uid] // list of uids
                    })
                }).then((r)=>{
                    return r.json();
                }).then((data)=>{
                    callback();
                })
            }
        })
    }
}
export { VideoController };