import { FetchApp } from "../components/functions/FetchApp"
import { ApplicationData } from "../data.application.route";


/**
 * 
 * @param {String} url 
 * @param {Object} parameters 
 * @returns {String}
 */
const server_connection = function(url, parameters){
    parameters = parameters || {};
    let instance = new FetchApp(url);
    instance.api_host = ApplicationData.get().api_host;
    instance.set_parameters(parameters);
    return instance.set_url(url);
}

/**
 * 
 * @param {String} url 
 * @param {Object} parameters 
 * @returns {String}
 */
 const server_app3pod = function(url, parameters){
    parameters = parameters || {};
    let instance = new FetchApp(url);
    instance.api_host = ApplicationData.get().app3pod_host;
    instance.set_parameters(parameters);
    return instance.set_url(url);
}

export {server_connection, server_app3pod}